import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import ManagerProjectScreen from './ManagerProjectScreen/ManagerProjectScreen';
import TeamMemberProjectScreen from './TeamMemberProjectScreen/TeamMemberProjectScreen';


const ProjectScreen = () => {
  const navigate = useNavigate();
  const { domain, username, projectId } = useParams();
  console.log('domain:', domain); 
  console.log('username:', username);
  console.log('projectId:', projectId);

  const [user, setUser] = useState(null);
  const [project, setProject] = useState(null);
  console.log('Domain:', domain);
  console.log('Username:', username);
  
  // Fetch user data from the server
  useEffect(() => {
    const token = localStorage.getItem('token');

    // If the token is not present, redirect to the login page
    if (!token) {
      navigate(`/PlanIt/login`);
      return;
    }

    const fetchUser = async () => {
        try {
            const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
            method: 'GET',
            headers: { Authorization: `Bearer ${token}` }
          });
          const data = await res.json();
          setUser(data);
          console.log('Fetched user:', data);
        } catch (error) {
          console.error('Error fetching user:', error);
        }
      };
  
      fetchUser();
  }, [username]);

  // Featch project data from the server
  useEffect(() => {
    const fetchProject = async () => {
        try {
            const res = await fetch(`/api/organizations/${domain}/users/${username}/projects/${projectId}`, {
            method: 'GET',
            headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
          });
          const data = await res.json();
          setProject(data);
          console.log('Fetched project:', data);
        } catch (error) {
          console.error('Error fetching project:', error);
        }
      };
  
      fetchProject();
}, [projectId]);

    if (!user) {
      return <div>No User Found...</div>;
  }
    if (!project) {
        return <div>No Project Found...</div>;
    }

  // Check if the user is a manager or a team member
  // and render the appropriate project screen
  if (user.role === 'manager') {
    return <ManagerProjectScreen user={user} domain={domain} project={project} />;
  } else if (user.role === 'team_member') {
    return <TeamMemberProjectScreen user={user} domain={domain} project={project} />;
  } else {
    return <ManagerProjectScreen user={user} domain={domain} project={project} />;
  }
};

export default ProjectScreen;
