import React from 'react';
import { PieChart, Pie, Cell, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import './OrganizationOverview.css';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#A28EF5', '#F56565'];

const OrganizationOverview = ({ team = [] }) => {
  const getTeamSize = (manager) => manager.team?.length || 0;

  const data = team
    .filter(member => member.role === 'manager')
    .map((manager, index) => ({
      name: `${manager.firstName} ${manager.lastName}`,
      value: manager.projects?.length || 0,
      color: COLORS[index % COLORS.length],
    }));

  const totalProjects = data.reduce((sum, item) => sum + item.value, 0);
  const totalUsers = team.reduce((sum, manager) => sum + 1 + getTeamSize(manager), 0);
  const totalManagers = team.filter(u => u.role === 'manager').length;

  return (
    <div className="overview-container">
      <h3>Organization Overview</h3>

      {data.length === 0 ? (
        <div className="empty-overview">
          <img
            src="https://www.svgrepo.com/show/327408/empty-box.svg"
            alt="No data"
            className="empty-illustration"
          />
          <p>No teams or projects yet</p>
          <p>Start by creating a manager or assigning projects to teams</p>
        </div>
      ) : (
        <ResponsiveContainer width="100%" height={300}>
          <PieChart>
            <Pie
              data={data}
              dataKey="value"
              nameKey="name"
              cx="50%"
              cy="50%"
              outerRadius={100}
              label
            >
              {data.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={entry.color} />
              ))}
            </Pie>
            <Tooltip />
            <Legend />
          </PieChart>
        </ResponsiveContainer>
      )}

      <div className="overview-stats">
        <div><strong>Total Projects:</strong> {totalProjects}</div>
        <div><strong>Total Users:</strong> {totalUsers}</div>
        <div><strong>Total teams:</strong> {totalManagers}</div>
      </div>
    </div>
  );
};

export default OrganizationOverview;
