import { useParams, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import ManagerMainScreen from './ManagerMainScreen/ManagerMainScreen';
import TeamMemberMainScreen from './TeamMemberMainScreen/TeamMemberMainScreen';
import OrganizationHeadMainScreen from './OrganizationHeadMainScreen/OrganizationHeadMainScreen';

const HomeScreen = () => {
    const navigate = useNavigate();
    const { domain, username } = useParams();
    const [user, setUser] = useState(null);

    // Fetch user data from the server
    useEffect(() => {
      const token = localStorage.getItem('token');

      // If the token is not present, redirect to the login page
      if (!token) {
        navigate(`/PlanIt/login`);
        return;
      }

      const fetchUser = async () => {
          try {
              const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
              method: 'GET',
              headers: { Authorization: `Bearer ${token}` }
            });
            const data = await res.json();
            setUser(data);
            console.log('Fetched user:', data);
          } catch (error) {
            console.error('Error fetching user:', error);
          }
        };
    
        fetchUser();
    }, [username]);

    if (!user) {
        return <div>No User Found...</div>;
    }


  // Check if the user is a manager or a team member
  // and render the appropriate main screen
  if (user.role === 'manager') {
    return <ManagerMainScreen user={user} domain={domain} />;
  } else if (user.role === 'team_member') {
    return <TeamMemberMainScreen user={user} domain={domain} />;
  } else if (user.role === 'organization_head') {
    return <OrganizationHeadMainScreen user={user} domain={domain} />;
  } else {
    return <div className="home-screen">User role is not recognized.</div>;
  }
};

export default HomeScreen;