import React from "react";
import TaskComment from "../TaskComment";
import './TaskCommentsCollection.css';

const TaskCommentsCollection = ({ comments, domain, username, projectId, taskId, onDelete }) => {
  if (!comments || comments.length === 0) return <h4>No comments yet</h4>;

  return (
    <div className="task-comments-collection">
      {comments.map((comment) => (
        <TaskComment
          key={comment._id}
          comment={comment}
          domain={domain}
          username={username}
          projectId={projectId}
          taskId={taskId}
          onDelete={onDelete}
        />
      ))}
    </div>
  );
};

export default TaskCommentsCollection;
