import React from 'react';
import './TagsCollection.css'; 
import Tag from '../Tag/Tag';

const TagsCollection = ({ tags, onClick =() => {}, onRemove, isLabel=false }) => {
  if (!tags || tags.length === 0) return <div></div>;

  return (
    <div className="tags-collection">
      {tags.map((tag, index) => (
        <Tag
          key={index}
          tag={tag}
          isLabel={isLabel}
          onClick={onClick}
          onRemove={onRemove}
        />
      ))}
    </div>
  );
};

export default TagsCollection;
