import React, { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import './ResetPasswordScreen.css';

const ResetPassword = () => {
  const { token } = useParams();
  const navigate = useNavigate();

  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  const isValidPassword = (pwd) => {
    return (
      pwd.length >= 8 &&
      /[A-Z]/.test(pwd) &&
      /[0-9]/.test(pwd) &&
      /[!@#$%^&*]/.test(pwd)
    );
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (password !== confirmPassword) {
      setMessage('Passwords do not match');
      return;
    }

    if (!isValidPassword(password)) {
      setMessage('Password does not meet criteria');
      return;
    }

    try {
      const res = await fetch('/api/reset-password', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ token, password }),
      });

      let data = {};
      const contentType = res.headers.get('content-type');

      if (contentType && contentType.includes('application/json')) {
        data = await res.json();
      } else {
        const text = await res.text();
        throw new Error(text); 
      }

      setMessage(data.message);

      if (res.ok && data.message === 'Password updated') {
        // login after reset
        const loginRes = await fetch('/api/tokens', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            username: data.username,
            password: password
          })
        });

        if (!loginRes.ok) {
          throw new Error('Login after reset failed');
        }

        const loginData = await loginRes.json();
        localStorage.setItem('token', loginData.token);

        navigate(`/PlanIt/${data.domain}/${data.username}/home`);
      }

    } catch (error) {
      console.error("Something went wrong:", error);
      setMessage(error.message || "Something went wrong. Please try again.");
    }
  };

  return (
    <div className="reset-container">
      <form className="reset-form" onSubmit={handleSubmit}>
        <h2>Set Your New Password</h2>

        <div className="input-group">
          <input
            type={showPassword ? 'text' : 'password'}
            placeholder="New Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <i className="bi bi-eye toggle-eye" onClick={() => setShowPassword(!showPassword)}></i>
        </div>

        <div className="input-group">
          <input
            type={showPassword ? 'text' : 'password'}
            placeholder="Confirm Password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          />
          <i className="bi bi-eye toggle-eye" onClick={() => setShowPassword(!showPassword)}></i>
        </div>

        <div className="criteria-list">
          <p style={{ color: password.length >= 8 ? 'green' : 'red' }}>At least 8 characters</p>
          <p style={{ color: /[A-Z]/.test(password) ? 'green' : 'red' }}>At least one uppercase letter</p>
          <p style={{ color: /[0-9]/.test(password) ? 'green' : 'red' }}>At least one number</p>
          <p style={{ color: /[!@#$%^&*]/.test(password) ? 'green' : 'red' }}>At least one special character (!@#$...)</p>
        </div>

        <button type="submit" className="submit-btn">Save</button>
        {message && <p className="status-message">{message}</p>}
      </form>
    </div>
  );
};

export default ResetPassword;
