// OrganizationHeadMainScreen.jsx
import Navbar from "../../../components/Navbar/Navbar";
import React from "react";
import 'bootstrap-icons/font/bootstrap-icons.css';
import MyTeam from "../Sections/MyTeam";
import MyNotifications from "../Sections/MyNotifications";
import OrganizationOverview from "../Sections/OrganizationOverview";
import "./OrganizationHeadMainScreen.css";
import { useParams } from "react-router-dom";

const OrganizationHeadMainScreen = ({ user }) => {
  const { domain, username } = useParams();

  const handleDeleteUser = async (member) => {
    const token = localStorage.getItem('token');
    try {
      const res = await fetch(`/api/organizations/${domain}/users/${member.username}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`
        }
      });

      if (!res.ok) throw new Error('Failed to delete user');
      alert(`${member.firstName} ${member.lastName} has been deleted.`);
      window.location.reload(); 
    } catch (error) {
      console.error("Error deleting user:", error);
      alert("Failed to delete user.");
    }
  };

  return (
    <div className="organization-head-main-screen">
      <Navbar user={user} domain={domain} />
      <div className="main-container">
        <div className="main-left">
        <OrganizationOverview team={user.team} style={{ width: "90%", height: "500px" }} />
        </div>
        <div className="main-right">
          <MyNotifications notifications={user.notifications} domain={domain} username={username} />
          <MyTeam team={user.team} user={user} onDeleteUser={handleDeleteUser} />
        </div>
      </div>
    </div>
  );
};

export default OrganizationHeadMainScreen;