import React, { useState, useEffect } from 'react';
import TagsCollection from '../TagsCollection/TagsCollection';
import './TagsInputContainer.css';

const TagsInputContainer = ({ selectedTags, setSelectedTags, height = '200px', width = '380px' }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [allTags, setAllTags] = useState([]);
    const [remainingTags, setRemainingTags] = useState([]);
   
    useEffect(() => {
    // API call to fetch tags
    const fetchTags = async () => {
        try {
            const res = await fetch(`/api/skills`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
          });
          const data = await res.json();
          setAllTags(data);
          setRemainingTags(data.filter(tag => !selectedTags.includes(tag)));
          console.log('Fetched tags:', data);
        } catch (error) {
          console.error('Error fetching tags:', error);
        }
      };
      fetchTags();
    }, []);

    useEffect(() => {
        console.log('Selected Tags (updated):', selectedTags);
        setRemainingTags(remainingTags.filter(tag => !selectedTags.includes(tag)));
        console.log('Remaining Tags (updated):', remainingTags);
      }, [selectedTags]);

    const handleSearchChange = (e) => {
        setSearchTerm(e.target.value);
    };

    const handleTagClick = (tag) => {
        if (!selectedTags.includes(tag)) {
            setSelectedTags([...selectedTags, tag]);
            setRemainingTags(remainingTags.filter((t) => t !== tag));
            console.log('selectedTags:', selectedTags);
            console.log('remainingTags:', remainingTags);
        }
    };

    const onTagRemove = (tag) => {
        setSelectedTags(selectedTags.filter((t) => t !== tag));
        setRemainingTags([...remainingTags, tag]);
    };

    const filteredTags = searchTerm
        ? remainingTags.filter((tag) =>
            tag?.label?.toLowerCase().includes(searchTerm.toLowerCase())
    )
        : [];

    return (
        <div 
            className="tags-input-container" 
            style={{ height, width }}
        >
            <input
                className="input-box"
                placeholder="Search tags"
                value={searchTerm}
                onChange={handleSearchChange}
            />
            {filteredTags.length > 0 && (
                <div className="tags-suggestions">
                <TagsCollection tags={filteredTags} onClick={handleTagClick} />
                </div>
            )}
            <div className="selected-tags">
                <TagsCollection tags={selectedTags} onRemove={onTagRemove} />
            </div>
        </div>
    );
};

export default TagsInputContainer;
