import React, { useState, useRef, useEffect } from 'react';
import './Navbar.css';
import Logo from '../../assets/Logo.png';
import ProfilePic from '../ProfilePics/ProfilePic/ProfilePic';
import SearchBar from './SearchBar/SearchBar';
import { useNavigate } from 'react-router-dom';

const Navbar = ({ user, domain }) => {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const dropdownRef = useRef(null);
  const navigate = useNavigate();

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setDropdownOpen(false);
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token');
    navigate('/PlanIt/login');
  };

  const handleLogoClick = () => {
    navigate(`/PlanIt/${domain}/${user.username}/home`);
  }

  if (!user) {
    return <div className="navbar">Loading...</div>;
  }

  return (
    <nav className="navbar">
      <img src={Logo} alt="Logo" className="logo" onClick={handleLogoClick}/>
      <div className="navbar-profile" ref={dropdownRef} onClick={() => setDropdownOpen(!dropdownOpen)}>
        <ProfilePic picture={user.profilePic} size={40} iconColor="white" />
        <h3 className="navbar-user">Hello, {user.firstName}</h3>

        {dropdownOpen && (
        <div className="profile-dropdown">
          <div
            className="profile-dropdown-item"
            onClick={() => navigate(`/PlanIt/${domain}/${user.username}/profile`)}
          >
            <i className="bi bi-person-circle"></i> View Profile
          </div>
          <div
            className="profile-dropdown-item"
            onClick={handleLogout}
          >
            <i className="bi bi-box-arrow-right"></i> Sign Out
          </div>
        </div>
      )}

      </div>
    </nav>
  );
};

export default Navbar;
