import React, { useState, useEffect } from "react";
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import "./CreateProject.css";
import 'bootstrap-icons/font/bootstrap-icons.css';
import InputBox from "../../../../components/InputBox/InputBox";
import Navbar from "../../../../components/Navbar/Navbar";
import TeamMembersSelection from "../TeamMembersSelection/TeamMembersSelection";
import TasksCollection from "../../../../components/Tasks/TasksCollection/TasksCollection";
import MainButton from "../../../../components/Buttons/MainButton/MainButton";
import NewTask from "../../../../components/Tasks/NewTask/NewTask";
import SecondaryButton from "../../../../components/Buttons/SecondaryButton/SecondaryButton";
import DescriptionBox from "../../../../components/DescriptionBox/DescriptionBox";

const CreateProject = () => {
  const navigate = useNavigate();
  const location = useLocation();

  // Extract domain and username from the URL parameters
  const { domain, username } = useParams();

  // State variables to manage project details, tasks, and user information
  const [user, setUser] = useState(null);
  const [deadlineType, setDeadlineType] = useState("date");
  const [isTaskModalOpen, setIsTaskModalOpen] = useState(false);

  // Extract existing tasks and project from the location state if available
  const { existingTasks = [], existingProject = null, team = []} = location.state || {};
  const [tasks, setTasks] = useState(existingTasks);
  const [project, setProject] = useState(existingProject || {
    title: "",
    description: "",
    deadline: "",
    teamMembers: [],
    tasks: [],
  });
  const [selectedMembers, setSelectedMembers] = useState(team);

  // Fetch user data from the server
  useEffect(() => {
      const fetchUser = async () => {
          try {
              const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
                  Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            });
            const data = await res.json();
            setUser(data);
            console.log('Fetched user:', data);
          } catch (error) {
            console.error('Error fetching user:', error);
          }
        };
    
        fetchUser();
  }, [username]);

  // Handle changes in the project form fields
  const handleChange = (e) => {
    const { name, value } = e.target;
    setProject((prev) => ({
      ...prev,
      tasks: tasks,
      [name]: value
    }));
  };

  // Handle form submission of project details
  const handleSubmit = async (e) => {
    e.preventDefault(); // Prevent default form submission behavior

    // Check if all required fields are filled
    if (!project.title || !project.description || !project.deadline || selectedMembers.length === 0 || tasks.length === 0) {
      alert("Please fill in all fields before proceeding.");
      return;
    }

    const saveProject = async () => {
      try {
          const res = await fetch(`/api/organizations/${domain}/users/${username}/projects`, {
          method: 'POST',
          headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
          body: JSON.stringify({
              title: project.title,
              description: project.description,
              deadline: project.deadline,
              team: selectedMembers,
              tasks: tasks, 
              avaliabilites: []
          }),
        });

        if (!res.ok) {
          throw new Error('Network response was not ok');
        }
          const data = await res.json();
          setProject(data);
          return data;
      } catch (error) {
        console.error('Error saving project:', error);
      }
    };

    const newProject = await saveProject();
    console.log('Creating project:', newProject);
    navigate(`/PlanIt/${domain}/${username}/projects/create/tasks`, { state: { project: newProject, tasks: tasks, team: selectedMembers, user: user } });
  };

  const handleTeamMembersGeneration = async () => {
    // API call to generate team members with AI
    try {
      const res = await fetch('/api/ai/generate-team-members', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({
          team: user.team,
          description: project.description
        })
      });

      if (!res.ok) {
        const errorText = await res.text();
        console.error("Failed to generate:", errorText);
        throw new Error("Failed to generate skills");
      }

      const data = await res.json();
      console.log("Generated team members:", data.teamMembers);
      setSelectedMembers([...selectedMembers, ...data.teamMembers]);

    } catch (error) {
      console.error('Error generating skills:', error);
    }
    
  };

  if (!user) {
    return null;
  }
  
  return (
    <div className="main-screen">
      <Navbar user={user} domain={domain} />
      <div className="main-container-create-project">
        <form className="main-container-create-project">
          <div className="main-left-create-project">
            <h2>Create Project</h2>
            <div className="project-details">
              <h3>Project Information</h3>
              <InputBox
                name="title"
                placeholder="Project Title"
                icon="bi-folder"
                value={project.title}
                onChange={handleChange}
                width="60%"
              />
              <DescriptionBox
                name="description"
                placeholder="Project Description"
                value={project.description}
                onChange={handleChange}
                icon="bi-chat-left-text" // Customizable icon
                width="60%" // Customizable width
                height="150px" // Customizable height
                fontSize="15px" // Customizable text size
              />
              <div className="team-members-header">
              <h3>Team Members</h3>
              <p className="generate-members" onClick={handleTeamMembersGeneration}>Generate Members</p>
              </div>
              <TeamMembersSelection team={user.team} selectedMembers={selectedMembers} setSelectedMembers={setSelectedMembers}/>
            </div>
          </div>

          <div className="main-right-create-project">
            <div className="deadline-section">
            <h3>Deadline</h3>
            <div className="deadline-options">
              <label>
                <input
                  type="radio"
                  checked={deadlineType === "date"}
                  onChange={() => setDeadlineType("date")}
                />
                Specific Date
              </label>
              <label style={{ marginLeft: "20px" }}>
                <input
                  type="radio"
                  checked={deadlineType === "range"}
                  onChange={() => setDeadlineType("range")}
                />
                Weeks From Now
              </label>
            </div>

            {deadlineType === "date" ? (
              <InputBox
                type="date"
                name="deadline"
                value={project.deadline}
                onChange={handleChange}
                icon="bi-calendar"
                width="100%"
              />
            ) : (
              <InputBox
                type="number"
                name="deadline"
                placeholder="Enter weeks"
                icon="bi-clock"
                onChange={handleChange}
                width="100%"
              />
            )}
            </div>
            <div className="create-tasks-section">
            <h3>Create Tasks</h3>
            <div className="tasks-container">
              <TasksCollection tasks={tasks} readOnly={false} loggedUser={user} clickable={false} setTasks={setTasks}/>
              <SecondaryButton
                title="New Task"
                type="button"
                icon={<i className="bi bi-plus-lg" />}
                onClick={() => setIsTaskModalOpen(true)}
                />
              <NewTask
                isOpen={isTaskModalOpen}
                onClose={() => setIsTaskModalOpen(false)}
                setTasks={setTasks}
                username={username}
                domain={domain}
              />
            </div>
            <div className="create-project-button">
            <MainButton title="Create and Start Planning" onClick={handleSubmit}/>
            </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreateProject;
