import React, { useState } from "react";
import { useParams, useLocation, useNavigate } from "react-router-dom";
import MainButton from "../../../../components/Buttons/MainButton/MainButton";
import SecondaryButton from "../../../../components/Buttons/SecondaryButton/SecondaryButton";
import InputBox from "../../../../components/InputBox/InputBox";
import Navbar from "../../../../components/Navbar/Navbar";
import TaskCollection from "../../../../components/Tasks/TasksCollection/TasksCollection";
import "./TasksAssignment.css";
import ProfilePic from "../../../../components/ProfilePics/ProfilePic/ProfilePic";

const SelectedTeamMembersAvaliability = ({ teamMembers, avaliabilites, setAvailabilites }) => {

  const handleAvailabilityChange = (memberId, value) => {
    setAvailabilites((prev) => ({
      ...prev,
      [memberId]: value,
    }));
  };

  return (
    <div className="selected-team-members-availability">
      <div className="team-members-list">
        {teamMembers.map((member) => (
          <div key={member._id} className="team-member-item">
            <div className="team-memeber-details">
              <ProfilePic picture={member.profilePic} size={32}/>
              <p className="team-member-name">{member.firstName} {member.lastName}</p>
              </div>
            <div className="team-member-availability">
              <label htmlFor={`hours-${member._id}`}>Hours per week:</label>
              <input
                type="number"
                id={`hours-${member._id}`}
                name={`hours-${member._id}`}
                min="0"
                max="40"
                value={avaliabilites[member._id]}
                onChange={(e) =>
                  handleAvailabilityChange(member._id, Number(e.target.value))
                }
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

const TasksAssignment = () => {

    const navigate = useNavigate();
    const location = useLocation();
    const { domain, username } = useParams();
    const { project = null, tasks = [], team = [], user = null } = location.state || {};
    const [assignedTasks, setAssignedTasks] = useState(tasks);

    // Correct state initialization for availability map
    const [avaliabilites, setAvailabilites] = useState(
      team.reduce((acc, member) => {
        acc[member._id] = 10; // Ensure default value is set
        return acc;
      }, {})
    );

    const [preferencesWeight, setPreferencesWeight] = useState(50); // Default weight is 50%

    const handlePreferencesWeightChange = (e) => {
        setPreferencesWeight(Number(e.target.value));
    };

    console.log("Project in TasksAssignment:", project);
    console.log("Project ID in TasksAssignment:", project?._id);
    console.log("Tasks in TasksAssignment:", tasks);
    console.log("Team in TasksAssignment:", team);
    const [assignments, setAssignments] = useState([]);

    const handleAssignmentGeneration = async () => {
        // API call to assign tasks
        const generateAssignments = async () => {
          try {
              const res = await fetch(`/api/loadBalance/${project._id}`, {
              method: 'PUT',
              headers: {
                  'Content-Type': 'application/json',
                  Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
                  body: JSON.stringify({
                  title: project.title,
                  description: project.description,
                  deadline: project.deadline,
                  team: team,
                  tasks: tasks,
                  avaliabilites: avaliabilites,
                  preferencesWeight: preferencesWeight / 100,
              }),
            });

            if (!res.ok) {
              throw new Error('Network response was not ok');
            }
              const data = await res.json();
              setAssignedTasks(data.tasks);
              console.log('Tasks have been assigned successfully');
              console.log('Assignments after API call:', data.tasks); // Log the new tasks directly
          } catch (error) {
            console.error('Error saving project:', error);
          }
        };
    
      await generateAssignments();
    };

    const handleSubmit = async (e) => {
      e.preventDefault();

      // API call for sending notifications to team members
      const sendNotifications = async () => {
        console.log("Sending notifications for project:", project._id, "domain:", domain, "username:", username);
        try {
          const res = await fetch(`/api/organizations/${domain}/users/${username}/projects/${project._id}/tasks/notifications`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          });
          if (!res.ok) {
            throw new Error('Failed to send notifications');
          }
          console.log('Notifications sent successfully');
          // Navigate to project page
          navigate(`/PlanIt/${domain}/${username}/projects/${project._id}`);
        } catch (error) {
          console.error('Error sending notifications:', error);
          return;
        }
      };
      await sendNotifications();
    };

    const handleBack = () => {
      navigate(`/PlanIt/${domain}/${username}/projects/${project._id}/edit`);
    };

    if (!project || !tasks || !team || !user) {
        return <div className="No project found">Loading...</div>;
    }

    return (
        <div className="main-screen">
            <Navbar user={user} domain={domain}/>
            <div className="tasks-assignment-main-screen">
                <div className="tasks-assignment-back-button" onClick={handleBack}>
                  <i className="bi bi-arrow-left"></i>
                </div>
                <div className="tasks-assignment-content">
                <h2 className="tasks-assignment-title">{project.title}</h2>
                <div className="tasks-assignment-team-and-tasks">
                  <div className="tasks-assignment-team">
                    <h3 className="tasks-assignment-title">Team's Availability</h3>
                    <SelectedTeamMembersAvaliability
                                teamMembers={team}
                                avaliabilites={avaliabilites}
                                setAvailabilites={setAvailabilites}
                            />
                  </div>
                  <div className="tasks-assignment-tasks">
                    <h3 className="tasks-assignment-title">Tasks Assignment</h3>
                    <TaskCollection 
                        tasks={assignedTasks} // Ensure assignedTasks is passed here
                        loggedUser={user} 
                        clickable={false} 
                        domain={domain} 
                        team={team} 
                    />
                  </div>
                </div>
                <div className="tasks-assignment-preferences-slider">
                    <label htmlFor="preferences-weight-slider">Preferences vs Urgency:</label>
                    <div className="slider-container">
                        <span className="slider-label">Preferences</span>
                        <input
                            type="range"
                            id="preferences-weight-slider"
                            name="preferences-weight-slider"
                            min="0"
                            max="100"
                            value={preferencesWeight}
                            onChange={handlePreferencesWeightChange}
                            color="var(--primary)"
                        />
                        <span className="slider-label">Urgency</span>
                    </div>
                    <span>{preferencesWeight}% Preferences</span>
                </div>
                <div className="tasks-assignment-bottom">
                    <SecondaryButton title="Assign Tasks" onClick={handleAssignmentGeneration}></SecondaryButton>
                    <MainButton title="Done" onClick={handleSubmit}></MainButton>
                <div/>
                </div>
                </div>
            </div>
        </div>
    );
}

export default TasksAssignment;