import React, { useState } from "react";
import Navbar from '../../../components/Navbar/Navbar';
import MyProjects from '../Sections/MyProjects';
import MyNotifications from '../Sections/MyNotifications';
import './TeamMemberMainScreen.css';
import MyTasks from '../Sections/MyTasks';
import MySkills from '../Sections/MySkills';


const TeamMemberMainScreen = ({ user, domain }) => {

  console.log("User in TeamMemberMainScreen:", user);
  const [notifications, setNotifications] = useState(user.notifications);
  

  const handleNotificationRemove = async (notification) => {
    console.log("Remove notification:", notification._id);

    try {
      const res = await fetch(`/api/organizations/${domain}/users/${user.username}/notifications/${notification._id}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      });

      if (!res.ok) {
        throw new Error('Failed to delete notification');
      }

      // Remove notification from state
      setNotifications((prevNotifications) =>
        prevNotifications.filter((n) => n._id !== notification._id)
      );
    } catch (error) {
      console.error("Error deleting notification:", error);
      alert("Failed to delete notification.");
    }
  };


  if (!user || user.role !== "team_member") {
    return <div className="member-main-screen">User data is unavailable.</div>;
  }

  return (
    <div className="team-member-main-screen">
      <Navbar user={user} domain={domain} />
      <div className="member-main-container">
                <div className="member-main-left">
                    <MyProjects projects={user.projects} loggedUser={user} style={{ width: "90%", height: "500px" }}/>
                    <MyTasks tasks = {user.tasks} user={user} domain={domain} style={{width: "90%", height: "600px"}}/>
                </div>
                <div className="member-main-right">
                <MyNotifications notifications={notifications} domain={domain} username={user.username} onRemove={handleNotificationRemove} />
                <MySkills user={user}/>
                </div>
      </div>
    </div>
  );
};

export default TeamMemberMainScreen;
