import React from "react";
import "./Skill.css";

const Skill = ({ skill }) => {
    
    console.log("Skill Data: ", skill);

    if (!skill) {
        console.log("No skill found...");
        return <div></div>;
    } 

    return (
        <div className="skill-container">
            <span className="skill-label">{skill.label}</span>
        </div>
    );
};

export default Skill;