import React, { useState, useEffect, useRef } from "react";
import "./TeamMembersSelection.css";
import ProfilePic from "../../../../components/ProfilePics/ProfilePic/ProfilePic";
import InputBox from "../../../../components/InputBox/InputBox";

const SelectedMembersSection = ({ members = [], onRemove }) => {
    return (
    <div className="selected-members-section"> 
        <h5 className="selected-members-label">Selected Members</h5>
        <div className="selected-members-container">
            {members.map((member, index) => (
                <div key={index} className="selected-member-item">
                    <div className="selected-member-info">
                        <ProfilePic picture={member.profilePic} size={35}/>
                        <h4 className="selected-member-name">{member.firstName + " " + member.lastName}</h4>
                    </div>
                    <button 
                        className="remove-member-button"
                        onClick={(e) => {
                            e.preventDefault(); // Prevent page reload
                            onRemove(member);
                        }}
                    >
                        Remove
                    </button>
                </div>
            ))}
        </div>
    </div>
    );
};

const UsersList = ({ filteredMembers, selectedMembers, setSelectedMembers, remaniningMembers, setRemainingMembers }) => {
    if (filteredMembers.length === 0) {
        return <div className="users-list">
            <p>No available users</p></div>;
    }

    return ( 
    <div className="users-list">
        {filteredMembers.map((member, index) => (
            <div
                key={index}
                className="member-item"
                onClick={() => {
                    if (!selectedMembers.includes(member)) {
                        setSelectedMembers([...selectedMembers, member]);
                        setRemainingMembers(remaniningMembers.filter((m) => m !== member));

                    }
                }}
            >
                <div className="user-suggestion">
                    <ProfilePic picture={member.profilePic} size={30}/>
                    <span>{member.firstName + " " + member.lastName}</span>
                </div>
            </div>
        ))}
    </div>
    );
};

const TeamMembersSelection = ({ team, selectedMembers, setSelectedMembers }) => {
    const [searchQuery, setSearchQuery] = useState('');
    const [remaniningMembers, setRemainingMembers] = useState(team);
    const [filteredMembers, setFilteredMembers] = useState(remaniningMembers);
    const [isFocused, setIsFocused] = useState(false);
    const containerRef = useRef(null);

    // Filter logic
    useEffect(() => {
        const lower = searchQuery.toLowerCase();
        if (searchQuery.trim() === '') {
            setFilteredMembers(remaniningMembers);
        } else {
            setFilteredMembers(
                remaniningMembers.filter((member) =>
                    `${member.firstName} ${member.lastName}`.toLowerCase().includes(lower)
                )
            );
        }
    }, [searchQuery, remaniningMembers]);    

    // Click outside handler
    useEffect(() => {
        const handleClickOutside = (event) => {
            if (containerRef.current && !containerRef.current.contains(event.target)) {
                setIsFocused(false);
            }
        };
    
        document.addEventListener("mousedown", handleClickOutside);
        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);    

    const handleRemoveMember = (member) => {
        setSelectedMembers(selectedMembers.filter((m) => m._id !== member._id));
        setRemainingMembers([...remaniningMembers, member]);
    };

    return (
        <div className="select-team-members-container" ref={containerRef}>
            <div className="team-members-selection">
                <InputBox
                    icon="bi-search"
                    placeholder="Search members..."
                    width="100%"
                    height="30px"
                    fontSize="14px"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    onClick={() => {
                        setIsFocused(true);
                        setFilteredMembers(remaniningMembers);
                    }}                    
                />
                {isFocused && (
                    <UsersList 
                        filteredMembers={filteredMembers} 
                        selectedMembers={selectedMembers}
                        setSelectedMembers={setSelectedMembers}
                        remaniningMembers={remaniningMembers}
                        setRemainingMembers={setRemainingMembers}
                    />
                )}
                <SelectedMembersSection 
                    members={selectedMembers} 
                    onRemove={handleRemoveMember} 
                />
            </div>
        </div>
    );
};

export default TeamMembersSelection;
