import React from 'react';
import Navbar from '../../../../components/Navbar/Navbar';
import './TeamMemberProjectScreen.css';
import ProjectDescription from '../../../MainScreens/Sections/ProjectDescription';
import MyTasks from '../../../MainScreens/Sections/MyTasks';
import TeamMemberProjectStats from './ProjectStats/TeamMemberProjectStats';

const TeamMemberProjectScreen = ({ user, domain, project }) => {
    if (!project) {
        return <div className="member-project-screen">Project data is unavailable.</div>;
    }

    return (
        <div className="member-project-screen">
            <Navbar user={user} domain={domain} />
            <div className="member-project-container">
                <div className="member-project-left">
                    <ProjectDescription project={project}/>
                    <div className="project-deadline">
                        <strong>Deadline:</strong> {project.deadline ? new Date(project.deadline).toLocaleDateString() : 'No deadline specified'}
                    </div>
                    <MyTasks tasks={user.tasks} user={user} domain={domain}/>
                </div>
                <div className="member-project-right">
                    <TeamMemberProjectStats project={project} user={user}/>
                </div>
            </div>
        </div>
    );
};

export default TeamMemberProjectScreen;