import React from 'react';
import SectionBox from '../../../components/Section/SectionBox';
import TasksCollection from '../../../components/Tasks/TasksCollection/TasksCollection';

const MyTasks = ({ tasks, user, style, onDelete, domain, sectionTitle="My Tasks" }) => {
  const taskCount = Array.isArray(tasks) ? tasks.length : 0;

  return (
    <SectionBox
      title={`${sectionTitle} (${taskCount})`}
      content={
        <TasksCollection
          tasks={tasks || []}
          user={user}
          readOnly={true}
          loggedUser={user}
          onDelete={onDelete} 
          domain={domain}
        />
      }
      style={style || { height: "400px", width: "90%" }}
    />
  );
};

export default MyTasks;
