import React, { useState, useEffect } from 'react';
import './Task.css';
import ProfilePic from '../../ProfilePics/ProfilePic/ProfilePic';
import TagsCollection from '../../Tags/TagsCollection/TagsCollection';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { useNavigate } from 'react-router-dom';
import EditTask from '../EditTask/EditTask';

const AssignmentUserList = ({ users, handleAssignUser }) => {
  console.log("Users in AssignmentUserList:", users);
  return (
    <div className="assignment-user-list">
      {users.map(user => (
        <div
          key={user._id}
          className="assignment-user-item"
          onClick={() => handleAssignUser(user)}
        >
          <ProfilePic picture={user.profilePic} size={30} />
          <span className="assignment-user-name">{user.firstName} {user.lastName}</span>
        </div>
      ))}
    </div>
  );
};

const Task = ({ task, onSave, onDelete, taskData, loggedUser, username, domain, team=[], clickable=true, setTasks=() => {} }) => {
  const navigate = useNavigate();
  const [updatesTask, setUpdatesTask] = useState({
    title: task.title,
    description: task.description,
    tags: task.tags,
    priority: task.priority || 'Medium',
  });
  const [assignedUser, setAssignedUser] = useState(task.assignedTo || null);
  const [assignmentChange, setAssignmentChange] = useState(false);
  const [isEditTaskModalOpen, setIsEditTaskModalOpen] = useState(false);

  console.log("Task data:", task);
  
  useEffect(() => {
    // Update assignedUser state whenever task.assignedTo changes
    setAssignedUser(task.assignedTo || null);
  }, [task.assignedTo]);

  const handleAssignUser = (user) => {
    setAssignedUser(user); // Update assignedUser state immediately
    setAssignmentChange(false); // Close assignment window

    const changeAssignment = async () => {
        try {
            const res = await fetch(`/api/organizations/${domain}/users/${username}/tasks/${task._id}/assign`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
                body: JSON.stringify({ user }),
            });

            if (!res.ok) {
                const errorData = await res.json(); // Log error response
                console.error('Error response:', errorData);
                throw new Error('Failed to update task');
            }

            const data = await res.json();
            console.log('Task updated successfully:', data);

            // Update task.assignedTo indirectly through state
            if (data.updatedTask) {
                setUpdatesTask((prev) => ({
                    ...prev,
                    assignedTo: data.updatedTask.assignedTo,
                }));
            }
        } catch (error) {
            console.error('Error updating task:', error);
            alert('Failed to update task');
        }
    };

    changeAssignment();
  };

  const handleAssignChange = () => {
    console.log('Assignment change triggered');
    setAssignmentChange(!assignmentChange);
  };

  const handleTaskClick = () => {
    if (!clickable) return;
    navigate(`/PlanIt/${loggedUser.organization.domain}/${loggedUser.username}/projects/${task.project}/tasks/${task._id}`);
  };

  const handleDelete = async () => {
    const confirmDelete = window.confirm("Are you sure you want to delete this task?");
    if (!confirmDelete) return;

    const token = localStorage.getItem("token");

    try {
      const url = `/api/organizations/${loggedUser.organization.domain}/users/${loggedUser.username}/projects/${task.project}/tasks/${task._id}`;

      const response = await fetch(url, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        }
      });

      if (response.ok) {
        if (onDelete) onDelete(task._id); 
      } else {
        alert("Failed to delete task (server error)");
      }
    } catch (err) {
      console.error("Failed to delete task:", err);
      alert("Failed to delete task (network error)");
    }
  };

  return (
    <div className="task-wrapper" onClick={handleTaskClick}>
      {assignmentChange && (
        <div className="assignment-window">
          <AssignmentUserList
            users={team} 
            handleAssignUser={handleAssignUser}
          />
        </div>
      )}
      {isEditTaskModalOpen ? (
        <EditTask
          taskToEdit={task}
          isOpen={isEditTaskModalOpen}
          onClose={() => setIsEditTaskModalOpen(false)}
          setTasks={setTasks}
          username={username}
          domain={domain}
        />
      ) : (
        <div className="task-container">
          <div className="task-content">
            <div className="task-left">
              <h2 className="task-name">{task.title}</h2>
              <div className="task-tags-container">
                <TagsCollection tags={task.tags} />
              </div>
            </div>
            <div className="task-right">
              <div className="task-profile" onClick={handleAssignChange}>
                {(assignedUser && <ProfilePic picture={assignedUser.profilePic} size={35} />) ||
                  <ProfilePic picture={null} size={35} />}
              </div>
              {task.priority && (
                <div className="priority">
                  <strong>{task.priority} Priority</strong>
                </div>
              )}
              <div className="task-status">
                <small>{task.status || 'Not Started'}</small>
              </div>
            </div>
          </div>

          <div className="edit-task-section">
            <button className="edit-task-button" onClick={(e) => {
              e.stopPropagation();
              setIsEditTaskModalOpen(true);
            }}>
              <i className="bi bi-pencil"></i>
            </button>
          </div>

          <div className="delete-task-section">
            <button className="delete-task-button" onClick={(e) => {
              e.stopPropagation();
              handleDelete();
            }}>
              <i className="bi bi-trash"></i>
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Task;
