import React from 'react';
import './NotificationsCollection.css';
import Notification from '../Notification/Notification';


const NotificationsCollection = ({ notifications, domain, username, onRemove }) => {
  const hasNotifications = Array.isArray(notifications) && notifications.length > 0;

  if (!hasNotifications) return <p>No notifications found.</p>;

  return (
    <div className="notifications-gallery">
      {notifications.map((notification, index) => (
        <Notification key={index} notification={notification} domain={domain} username={username} onRemove={onRemove} />
      ))}
    </div>
  );
};

export default NotificationsCollection;