import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from 'react-router-dom';
import '../CreateProject/CreateProject.css'; // Use CreateProject.css for styling
import 'bootstrap-icons/font/bootstrap-icons.css';
import InputBox from "../../../../components/InputBox/InputBox";
import Navbar from "../../../../components/Navbar/Navbar";
import TeamMembersSelection from "../TeamMembersSelection/TeamMembersSelection";
import TasksCollection from "../../../../components/Tasks/TasksCollection/TasksCollection";
import MainButton from "../../../../components/Buttons/MainButton/MainButton";
import NewTask from "../../../../components/Tasks/NewTask/NewTask";
import SecondaryButton from "../../../../components/Buttons/SecondaryButton/SecondaryButton";

const EditProject = () => {
  const navigate = useNavigate();
  const { domain, username, projectId } = useParams();

  const [user, setUser] = useState(null);
  const [deadlineType, setDeadlineType] = useState("date");
  const [isTaskModalOpen, setIsTaskModalOpen] = useState(false);
  const [tasks, setTasks] = useState([]);
  const [project, setProject] = useState({
    title: "",
    description: "",
    deadline: "",
    teamMembers: [],
    tasks: [],
  });
  const [selectedMembers, setSelectedMembers] = useState([]);

  useEffect(() => {
    const fetchProject = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${username}/projects/${projectId}`, {
            method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        });
        const data = await res.json();
        setProject(data);
        setTasks(data.tasks);
        setSelectedMembers(data.team);
        console.log('Fetched project:', data);
      } catch (error) {
        console.error('Error fetching project:', error);
      }
    };

    const fetchUser = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        });
        const data = await res.json();
        setUser(data);
        console.log('Fetched user:', data);
      } catch (error) {
        console.error('Error fetching user:', error);
      }
    };

    fetchUser();
    fetchProject();
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProject((prev) => ({
      ...prev,
      tasks: tasks,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('Editing project:', project);

    const updateProject = async () => {
      try {
          const res = await fetch(`/api/organizations/${domain}/users/${username}/projects/${project._id}`, {
          method: 'PUT',
          headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
          body: JSON.stringify({title: project.title,
            description: project.description,
            deadline: project.deadline,
            team: selectedMembers,
            tasks: tasks, 
            avaliabilites: []}),
        });

        if (!res.ok) {
          throw new Error('Network response was not ok');
        }
          const data = await res.json();
          setProject(data);
          return data;
      } catch (error) {
        console.error('Error saving project:', error);
      }
    };

    const newProject = await updateProject();
    console.log('Updated project:', newProject);
    navigate(`/PlanIt/${domain}/${username}/projects/${projectId}/edit/tasks`, {
      state: {
        project: newProject,
        tasks: tasks,
        team: selectedMembers,
        user: user,
      },
    });
  };

  if (!user) {
    return null;
  }

  return (
    <div className="main-screen">
      <Navbar user={user} domain={domain} />
      <div className="main-container-create-project"> {/* Updated class name */}
        <form className="main-container-create-project"> {/* Updated class name */}
          <div className="main-left-create-project"> {/* Updated class name */}
            <h2>Edit Project</h2>
            <div className="project-details">
              <h3>Project Information</h3>
              <InputBox
                name="title"
                placeholder="Project Title"
                icon="bi-folder"
                value={project.title}
                onChange={handleChange}
                width="60%"
              />
              <InputBox
                name="description"
                placeholder="Project Description"
                icon="bi-chat-left-text"
                textarea
                value={project.description}
                onChange={handleChange}
                rows={4}
                height="100px"
                width="60%"
              />
              <h3>Team Members</h3>
              <TeamMembersSelection team={user.team} selectedMembers={selectedMembers} setSelectedMembers={setSelectedMembers} />
            </div>
          </div>

          <div className="main-right-create-project"> {/* Updated class name */}
            <div className="deadline-section">
              <h3>Deadline</h3>
              <div className="deadline-options">
                <label>
                  <input
                    type="radio"
                    checked={deadlineType === "date"}
                    onChange={() => setDeadlineType("date")}
                  />
                  Specific Date
                </label>
                <label style={{ marginLeft: "20px" }}>
                  <input
                    type="radio"
                    checked={deadlineType === "range"}
                    onChange={() => setDeadlineType("range")}
                  />
                  Weeks From Now
                </label>
              </div>

              {deadlineType === "date" ? (
                <InputBox
                  type="date"
                  name="deadline"
                  value={project.deadline}
                  onChange={handleChange}
                  icon="bi-calendar"
                  width="100%"
                />
              ) : (
                <InputBox
                  type="number"
                  name="deadline"
                  placeholder="Enter weeks"
                  icon="bi-clock"
                  onChange={handleChange}
                  width="100%"
                />
              )}
            </div>
            <div className="create-tasks-section"> {/* Updated class name */}
              <h3>Edit Tasks</h3>
              <div className="tasks-container">
                <TasksCollection tasks={tasks} readOnly={false} loggedUser={user} clickable={false}/>
                <SecondaryButton
                  title="New Task"
                  type="button"
                  icon={<i className="bi bi-plus-lg" />}
                  onClick={() => setIsTaskModalOpen(true)}
                />
                <NewTask
                  isOpen={isTaskModalOpen}
                  onClose={() => setIsTaskModalOpen(false)}
                  setTasks={setTasks}
                  username={username}
                  domain={domain}
                />
              </div>
              
            </div>
            <div className="create-project-button"> {/* Updated class name */}
                <MainButton title="Assign Tasks" onClick={handleSubmit} />
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditProject;
