import { useParams, useNavigate } from 'react-router-dom';
import { useEffect, useState } from 'react';
import RegistrationForManager from './RegistrationForManager/RegistrationForManager';
import RegistrationOfTeamMember from './RegistrationOfTeamMember/RegistrationOfTeamMember';

const UserRegistration = () => {
  const { domain, username } = useParams();
  const navigate = useNavigate();
  const [user, setUser] = useState(null);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate(`/PlanIt/login`);
      return;
    }

    const fetchUser = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
          method: 'GET',
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!res.ok) {
          throw new Error('Failed to fetch user');
        }

        const data = await res.json();
        setUser(data);
      } catch (error) {
        console.error('Error fetching user:', error.message);
      }
    };

    fetchUser();
  }, [domain, username, navigate]);

  if (!user) {
    return <div>Loading...</div>;
  }

  if (user.role === 'organization_head') {
    return <RegistrationForManager user={user} />;
  } else if (user.role === 'manager') {
    return <RegistrationOfTeamMember user={user} />;
  } else {
    return <div>Unauthorized</div>;
  }
};

export default UserRegistration;
