import React, { useState } from 'react';
import './SignUp.css';
import InputBox from '../../../../components/InputBox/InputBox';
import MainButton from '../../../../components/Buttons/MainButton/MainButton';
import { useNavigate } from 'react-router-dom';

const SignUp = () => {
  const navigate = useNavigate();

  const [organizationData, setOrganizationData] = useState({
    organizationName: '',
    organizationDescription: '',
    profilePic: '',
    organizationEmail: '',
    organizationUsername: '',
    organizationPassword: '',
    headFirstName: '',
    headLastName: '',
  });

  const [errors, setErrors] = useState({});
  const [showPassword, setShowPassword] = useState(false);
  const [showPasswordCriteria, setShowPasswordCriteria] = useState(false);

  const togglePasswordVisibility = () => {
    setShowPassword((prev) => !prev);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setOrganizationData((prev) => ({
      ...prev,
      [name]: value,
    }));
    setErrors((prev) => ({ ...prev, [name]: '' }));
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = () => {
      setOrganizationData((prev) => ({
        ...prev,
        profilePic: reader.result,
      }));
    };
    reader.readAsDataURL(file);
  };

  const validateForm = () => {
    const newErrors = {};

    if (!organizationData.organizationEmail.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
      newErrors.organizationEmail = 'Invalid email format.';
    }
    if (!organizationData.organizationUsername || !/^[a-zA-Z]+$/.test(organizationData.organizationUsername)) {
      newErrors.organizationUsername = 'Username must contain only letters.';
    }
    if (!organizationData.organizationPassword || organizationData.organizationPassword.length < 6) {
      newErrors.organizationPassword = 'Password must be at least 6 characters.';
    }
    if (!organizationData.organizationName) {
      newErrors.organizationName = 'Organization name is required.';
    }
    if (!organizationData.headFirstName || !/^[a-zA-Z]+$/.test(organizationData.headFirstName)) {
      newErrors.headFirstName = 'First name must contain only letters.';
    }
    if (!organizationData.headLastName || !/^[a-zA-Z]+$/.test(organizationData.headLastName)) {
      newErrors.headLastName = 'Last name must contain only letters.';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!validateForm()) return;

    try {
      const availabilityRes = await fetch(`/api/users/check-availability`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email: organizationData.organizationEmail,
          username: organizationData.organizationUsername,
        }),
      });

      const dataAvailability = await availabilityRes.json();
      const newErrors = {};
      if (dataAvailability.emailTaken) newErrors.organizationEmail = 'Email is already in use.';
      if (dataAvailability.usernameTaken) newErrors.organizationUsername = 'Username is already taken.';
      if (Object.keys(newErrors).length > 0) {
        setErrors((prev) => ({ ...prev, ...newErrors }));
        return;
      }
      
      const res = await fetch('/api/organizations', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(organizationData),
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.error || 'Registration failed');
      }

      const data = await res.json();
      localStorage.setItem('token', data.token);
      alert('Organization created successfully!');
      navigate(`/PlanIt/${data.organization.domain}/${data.user.username}/home`);
    } catch (error) {
      console.error('Error during registration:', error.message);
      alert(error.message);
    }
  };

  const isValidPassword = (pwd) => {
    return (
      pwd.length >= 8 &&
      /[A-Z]/.test(pwd) &&
      /[0-9]/.test(pwd) &&
      /[!@#$%^&*]/.test(pwd)
    );
  };

  return (
    <form className="auth-form" onSubmit={handleSubmit}>
      <h2 className="auth-title">Create Account</h2>
      <div className="form-row">
        <div className="form-section">
          <h3 className="section-title">Organization Details</h3>
          <InputBox
            icon="bi-envelope-fill"
            placeholder="Email"
            type="email"
            name="organizationEmail"
            value={organizationData.organizationEmail}
            onChange={handleChange}
            width="100%"
            borderColor={errors.organizationEmail ? '#dc3545' : null}
          />
          <p className="field-error-text">{errors.organizationEmail}</p>

          <InputBox
            icon="bi-person-fill"
            placeholder="Username"
            type="text"
            name="organizationUsername"
            value={organizationData.organizationUsername}
            onChange={handleChange}
            width="100%"
            borderColor={errors.organizationUsername ? '#dc3545' : null}
          />
          <p className="field-error-text">{errors.organizationUsername}</p>

          <div style={{ position: 'relative', width: '100%' }}>
          <InputBox
            icon="bi-lock-fill"
            placeholder="Password"
            type={showPassword ? 'text' : 'password'}
            name="organizationPassword"
            value={organizationData.organizationPassword}
            onChange={handleChange}
            onFocus={() => {
              console.log('Password field focused');
              setShowPasswordCriteria(true);
            }}
            onBlur={() => {
              console.log('Password field blurred');
              setShowPasswordCriteria(false);
            }}
            width="100%"
            borderColor={errors.organizationPassword ? '#dc3545' : null}
          />
          <button
          type="button"
          className="toggle-password-visibility"
          onClick={togglePasswordVisibility}
          style={{
            position: 'absolute',
            right: '1px',
            top: '50%',
            transform: 'translateY(-50%)',
            background: 'none',
            border: 'none',
            cursor: 'pointer',
          }}
        >
          <i className={`bi ${showPassword ? 'bi-eye-slash-fill' : 'bi-eye-fill'}`}></i>
        </button>
        {showPasswordCriteria && (
              <div className="password-criteria-popup">
                <p style={{ color: organizationData.organizationPassword.length >= 8 ? 'green' : 'red' }}>At least 8 characters</p>
                <p style={{ color: /[A-Z]/.test(organizationData.organizationPassword) ? 'green' : 'red' }}>At least one uppercase letter</p>
                <p style={{ color: /[0-9]/.test(organizationData.organizationPassword) ? 'green' : 'red' }}>At least one number</p>
                <p style={{ color: /[!@#$%^&*]/.test(organizationData.organizationPassword) ? 'green' : 'red' }}>At least one special character (!@#$...)</p>
              </div>
            )}
        </div>

          <p className="field-error-text">{errors.organizationPassword}</p>
          <InputBox
            icon="bi-building"
            placeholder="Name of Organization"
            type="text"
            name="organizationName"
            value={organizationData.organizationName}
            onChange={handleChange}
            width="100%"
            borderColor={errors.organizationName ? '#dc3545' : null}
          />
          <p className="field-error-text">{errors.organizationName}</p>

          <InputBox
            icon="bi-card-text"
            placeholder="Description"
            type="text"
            name="organizationDescription"
            value={organizationData.organizationDescription}
            onChange={handleChange}
            width="100%"
          />
          <div className="upload-field">
            <label htmlFor="organizationProfilePicture">Upload Profile Picture:</label>
            <input type="file" accept="image/*" onChange={handleFileChange} />
          </div>
        </div>

        <div className="form-section">
          <h3 className="section-title">Head Details</h3>
          <InputBox
            icon="bi-person-fill"
            placeholder="First Name"
            type="text"
            name="headFirstName"
            value={organizationData.headFirstName}
            onChange={handleChange}
            width="130%"
            borderColor={errors.headFirstName ? '#dc3545' : null}
          />
          <p className="field-error-text">{errors.headFirstName}</p>

          <InputBox
            icon="bi-person-fill"
            placeholder="Last Name"
            type="text"
            name="headLastName"
            value={organizationData.headLastName}
            onChange={handleChange}
            width="130%"
            borderColor={errors.headLastName ? '#dc3545' : null}
          />
          <p className="field-error-text">{errors.headLastName}</p>
        </div>
      </div>
      <MainButton title="Sign Up" type="submit" />
    </form>
  );
};

export default SignUp;
