import React, { useState, useEffect } from 'react';
import './EditTask.css';
import '../../InputBox/InputBox.css';
import MainButton from '../../Buttons/MainButton/MainButton';
import TagsInputContainer from '../../Tags/TagsInputContainer/TagsInputContainer';

const EditTask = ({ isOpen, onClose, setTasks, username, domain, taskToEdit, setTaskToEdit}) => {
    const [selectedTags, setSelectedTags] = useState(taskToEdit?.tags || []);
    const [task, setTask] = useState({
        title: taskToEdit?.title || '',
        description: taskToEdit?.description || '',
        dueDate: taskToEdit?.dueDate || '',
        priority: taskToEdit?.priority || '',
    });

    useEffect(() => {
        if (taskToEdit) {
            setTask({
                title: taskToEdit.title,
                description: taskToEdit.description,
                dueDate: taskToEdit.dueDate,
                priority: taskToEdit.priority,
            });
            setSelectedTags(taskToEdit.tags || []);
        }
    }, [taskToEdit]);

    const generateAITags = async () => {
        if (!task.description) {
            alert('Please provide a description to generate AI tags.');
            return;
        }
        try {
            const res = await fetch(`/api/ai/generate-tags`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
                body: JSON.stringify({ description: task.description }),
            });
            if (!res.ok) {
                throw new Error(`Failed to generate AI tags: ${res.statusText}`);
            }
            const data = await res.json();
            setSelectedTags(data.tags);
            console.log('AI-generated tags:', data.tags);
        } catch (error) {
            console.error('Error generating AI tags:', error);
            alert('Failed to generate AI tags');
        }
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setTask((prevTask) => ({
            ...prevTask,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        // Validation to check if all fields are filled
        if (!task.title || !task.description || !task.priority || selectedTags.length === 0) {
            alert('Please fill out all fields and select at least one tag before saving the task.');
            return;
        }
        // API call to update the existing task
        const updateTask = async () => {
            try {
                const res = await fetch(`/api/organizations/${domain}/users/${username}/tasks/${taskToEdit._id}`, {
                    method: 'PUT',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${localStorage.getItem('token')}`,
                    },
                    body: JSON.stringify({ title: task.title, description: task.description, dueDate: task.dueDate, priority: task.priority, tags: selectedTags }),
                });

                if (!res.ok) {
                    throw new Error(`Failed to update task: ${res.statusText}`);
                }

                const updatedTask = await res.json();
                console.log('Task updated successfully:', updatedTask);

                // Update the tasks state to reflect the changes in the UI
                setTask(updateTask);
                setTasks((prevTasks) =>
                    prevTasks.map(t => (t._id === updatedTask._id ? updatedTask : t))
                  );
                  
                  

            } catch (error) {
                console.error('Error updating task:', error);
                alert('Failed to update task');
            }
        };

        await updateTask();

        // Reset task and tags to initial state
        setSelectedTags([]);
        setTask({
            title: '',
            description: '',
            dueDate: '',
            priority: '',
        });
        onClose();
    };

    if (!isOpen) 
        return null;

    return (
        <div className="modal-overlay">
            <div className="edit-task">
                <div className="edit-task-body">
                    <div className="edit-task-header">
                        <div className='close-button-container'>
                            <button className="close-button" onClick={onClose}>×</button>
                        </div>
                        <h3>Edit Task</h3>
                    </div>
                    <form className="task-details" onSubmit={handleSubmit}>
                        <input className="input-box" style={{ height: "40px", fontSize: "16px" }}
                        type="text"
                        name="title"
                        placeholder="Task Title"
                        value={task.title}
                        onChange={handleChange}
                        required
                        />
                        <textarea className="input-box" style={{ height: "150px" }} 
                        name="description"
                        placeholder="Task Description"
                        value={task.description}
                        onChange={handleChange}
                        ></textarea>
                        <select name="priority" value={task.priority} onChange={handleChange}>
                        <option value="" disabled>
                        Select Priority </option>
                        <option value="Low">Low</option>
                        <option value="Medium">Medium</option>
                        <option value="High">High</option>
                        </select>
                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                            <p className='tags-input-title'>Tags</p>
                            <button type="button" className="ai-tags-button" onClick={generateAITags}>
                                Generate AI Tags
                            </button>
                        </div>
                        <TagsInputContainer selectedTags={selectedTags} setSelectedTags={setSelectedTags} />
                    </form>
                </div>
                <MainButton title="Save Changes" type="submit" onClick={handleSubmit}/>
            </div>
        </div>
    );
}

export default EditTask;