import React from 'react';
import './TasksCollection.css';
import Task from '../Task/Task';

const TasksCollection = ({ tasks, readOnly, loggedUser, domain, onDelete, clickable, team, setTasks }) => {
  if (!tasks || tasks.length === 0) return <p>No tasks found.</p>;

  return (
    <div className="tasks-box">
      {tasks.map((task) => (
        <div key={task._id} className="task-wrapper">
          <Task
            task={task}
            readOnly={readOnly}
            loggedUser={loggedUser}
            assignedUser={task.assignedTo} // Ensure assignedUser is passed correctly
            onDelete={onDelete} 
            clickable={clickable}
            username={loggedUser.username}
            domain={domain}
            team={team}
            setTasks={setTasks}
          />
        </div>
      ))}
    </div>
  );
};

export default TasksCollection;
