import React from "react";
import "./DescriptionBox.css";
import 'bootstrap-icons/font/bootstrap-icons.css';

const DescriptionBox = ({
  name = "description",
  placeholder = "Enter a detailed description...",
  value = "",
  onChange = () => {},
  rows = 8,
  style = {},
  width = "100%",
  height = "200px",
  fontSize = "16px", // Added fontSize customization
  icon = "bi-chat-left-text",
  iconColor = "var(--text_secondary)",
}) => {
  return (
    <div className="description-box" style={{ width }}>
      <div className="description-icon">
        <i className={`bi ${icon}`} style={{ color: iconColor, fontSize: `calc(${fontSize} + 2px)` }} />
      </div>
      <textarea
        id={name}
        name={name}
        value={value}
        onChange={onChange}
        rows={rows}
        placeholder={placeholder}
        style={{ resize: "vertical", height, fontSize, ...style }} // Applied fontSize
      />
    </div>
  );
};

export default DescriptionBox;
