import { React, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { socket } from './socket';
import './App.css';

import AuthSplitScreen from './screens/AuthScreens/AuthSplitScreen/AuthSplitScreen';
import HomeScreen from './screens/MainScreens/HomeScreen';
import UserRegistration from './screens/AuthScreens/UserRegistration/UserRegistration';
import ProjectScreen from './screens/ProjectScreens/ProjectMainScreen/ProjectScreen';
import TaskScreen from './screens/TaskScreen/TaskScreen';
import TasksAssignment from './screens/ProjectScreens/ProjectCreation/TasksAssignment/TasksAssignment';
import CreateProject from './screens/ProjectScreens/ProjectCreation/CreateProject/CreateProject';
import EditProject from './screens/ProjectScreens/ProjectCreation/EditProject/EditProject';
import TeamScreen from './screens/TeamScreen/TeamScreen';
import RegistrationForManager from './screens/AuthScreens/UserRegistration/RegistrationForManager/RegistrationForManager';
import RegistrationOfTeamMember from './screens/AuthScreens/UserRegistration/RegistrationOfTeamMember/RegistrationOfTeamMember';
import ResetPasswordScreen from './screens/AuthScreens/ResetPasswordScreen/ResetPasswordScreen';
import GenerateSkills from './screens/AuthScreens/UserRegistration/RegistrationOfTeamMember/GenerateSkills';
import ForgotPassword from './screens/AuthScreens/ForgotPassword/ForgotPassword';
import EditTeamMember from './screens/ProfileScreen/EditTeamMember/EditTeamMemberFirstScreen';
import EditTeamMemberSkills from './screens/ProfileScreen/EditTeamMember/EditTeamMemberSecondScreen';
import EditManager from './screens/ProfileScreen/EditManager/EditManagerScreen';
import ProfileScreen from './screens/ProfileScreen/ProfileSceeen/ProfileScreen';

function App() {

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      socket.auth.token = token;
      socket.connect();
    }
  
    socket.on('notification', (data) => {
      console.log('New notification:', data);
    });
  
    return () => {
      socket.disconnect();
    };
  }, []);
  

  return (
    <div className="App">
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Navigate to="/PlanIt/login"/>} />
          <Route path="/PlanIt/login" element={<AuthSplitScreen />} />
          
          {/* Main Screen */}
          <Route path="/PlanIt/:domain/:username/home" element={<HomeScreen />} />

          {/* Team manager / Team member Registration */}
          <Route path="/PlanIt/:domain/:username/registration" element={<UserRegistration />} />

          {/* Project Screen */}
          <Route path="/PlanIt/:domain/:username/projects/:projectId" element={<ProjectScreen />} />

          {/* Task Screen */}
          <Route path="/PlanIt/:domain/:username/projects/:projectId/tasks/:taskId" element={<TaskScreen/>} />

          {/* Project Create */}
          <Route path="/PlanIt/:domain/:username/projects/create" element={<CreateProject />} />
          <Route path="/PlanIt/:domain/:username/projects/create/tasks" element={<TasksAssignment />} />

          {/* Project Edit */}
          <Route path="/PlanIt/:domain/:username/projects/:projectId/edit" element={<EditProject />} />
          <Route path="/PlanIt/:domain/:username/projects/:projectId/edit/tasks" element={<TasksAssignment />} />

          {/* Login Screen */}
          <Route path="/PlanIt/login" element={<AuthSplitScreen />} />

          {/* Team Manager Resigration */}
          <Route path="/PlanIt/:domain/manager/registration" element={<RegistrationForManager />} />

          {/* Team Member Registration */}
          <Route path="/PlanIt/:domain/member/registration" element={<RegistrationOfTeamMember />} />
          <Route path="/PlanIt/:domain/:username/generate-skills" element={<GenerateSkills />} />

           {/* Edit Team Member */}
          <Route path="/PlanIt/:domain/:managerUsername/:username/edit" element={<EditTeamMember />} />
          <Route path="/PlanIt/:domain/:managerUsername/:username/edit-skills" element={<EditTeamMemberSkills />} />
          {/* Edit Manager */}
          <Route path="/PlanIt/:domain/:managerUsername/:username/edit-manager" element={<EditManager />} />

          {/*team member */}
          <Route path="/PlanIt/:domain/:username/team" element={<TeamScreen />} />


          {/* User profile */}
          <Route path="/PlanIt/:domain/:username/profile" element={<ProfileScreen />} />

          {/*Change Password */}

          <Route path="/reset-password/:token" element={<ResetPasswordScreen />} />
          <Route path="/forgot-password" element={<ForgotPassword />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;