import React from "react";
import SectionBox from '../../../components/Section/SectionBox';
import "./MyTeam.css";
import ProfilePic from "../../../components/ProfilePics/ProfilePic/ProfilePic";
import { useNavigate, useParams } from "react-router-dom";

const TeamGallery = ({ team, user, onDelete }) => {
  return (
    <div className="team-gallery">
      {team.map((member, index) => {
        const fullName = `${member.firstName || ''} ${member.lastName || ''}`;

        return (
          <div key={index} className="team-member-container">
            <ProfilePic
              picture={member.profilePic}
              size={40}
              alt={fullName}
              className="team-member-pic"
            />
            <div className="team-member-name">{fullName}</div>
            {(user.role === 'organization_head' || user.role === 'manager') && (
              <button
                className="delete-user-button"
                onClick={() => {
                  const confirmDelete = window.confirm(`Are you sure you want to delete ${fullName}?`);
                  if (confirmDelete) onDelete(member);
                }}
              >
                <i className="bi bi-trash"></i>
              </button>
            )}
          </div>
        );
      })}
    </div>
  );
};

const MyTeam = ({ team, user, style, onDeleteUser, sectionTitle="My Team" }) => {
  const navigate = useNavigate();
  const { domain, username } = useParams();
  const defaultStyle = { width: "180%", height: "200px" };

  const handleAddUser = () => {
    navigate(`/PlanIt/${domain}/${username}/registration`);
  };

  const handleViewAll = () => {
    navigate(`/PlanIt/${domain}/${username}/team`);
  };

  const isManager = user && (user.role === 'organization_head' || user.role === 'manager');

  return (
    <SectionBox
      title={
        <div style={{ display: "flex", alignItems: "center", gap: "12px" }}>
          <span>{sectionTitle}</span>
          {isManager && (
            <i
              className="bi bi-plus-circle"
              style={{ cursor: "pointer", fontSize: "1.5rem" }}
              onClick={handleAddUser}
            ></i>
          )}
        </div>
      }
      content={<TeamGallery team={team || []} user={user} onDelete={onDeleteUser} />}
      style={style || defaultStyle}
      showViewAll={isManager}
      onViewAllClick={handleViewAll}
    />
  );
};

export default MyTeam;
