import React, { useState, useEffect } from 'react';
import MainButton from '../../../components/Buttons/MainButton/MainButton';
import Navbar from '../../../components/Navbar/Navbar';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import TagsCollection from '../../../components/Tags/TagsCollection/TagsCollection';
import TagsInputContainer from '../../../components/Tags/TagsInputContainer/TagsInputContainer';
import DescriptionBox from '../../../components/DescriptionBox/DescriptionBox';
import './EditTeamMemberSecondScreen.css';

const EditTeamMemberSkills = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { domain, managerUsername } = useParams();
  const { formData, user, managerUser } = location.state || {};

  const [description, setDescription] = useState('');
  const [unmatchedSkills, setUnmatchedSkills] = useState([]);
  const [unmatchedPreferences, setUnmatchedPreferences] = useState([]);
  const [selectedSkills, setSelectedSkills] = useState([]);
  const [selectedPreferences, setSelectedPreferences] = useState([]);

  useEffect(() => {
    if (user) {
      const initialSkills = Array.isArray(user.skills)
        ? user.skills.map(s => (typeof s === 'string' ? { label: s } : s))
        : [];

      const initialPreferences = Array.isArray(user.preferences)
        ? user.preferences.map(p => (typeof p === 'string' ? { label: p } : p))
        : [];

      setSelectedSkills(initialSkills);
      setSelectedPreferences(initialPreferences);
    }
  }, [user]);

  const handleGenerate = async () => {
    try {
      const res = await fetch('/api/ai/generate-skills', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({ description }),
      });

      if (!res.ok) {
        const errorText = await res.text();
        console.error("Failed to generate:", errorText);
        throw new Error("Failed to generate skills");
      }

      const data = await res.json();

      const generatedSkills = data.skills || [];
      const generatedPreferences = data.preferences || [];
      const unmatchedSkillsList = data.unmatchedSkills || [];
      const unmatchedPreferencesList = data.unmatchedPreferences || [];

      setSelectedSkills([...selectedSkills, ...generatedSkills]);
      setSelectedPreferences([... selectedPreferences, ...generatedPreferences]);

      setUnmatchedSkills(unmatchedSkillsList);
      setUnmatchedPreferences(unmatchedPreferencesList);

      console.log("Generated skills:", generatedSkills);
      console.log("Generated preferences:", generatedPreferences);
      console.log("Unmatched skills:", unmatchedSkillsList);
      console.log("Unmatched preferences:", unmatchedPreferencesList);

    } catch (error) {
      console.error('Error generating skills:', error);
    }
  };

  const handleAddNewTag = async (tagName, type) => {
    console.log(`Adding new ${type}:`, tagName);
    if (!tagName) return;
    const token = localStorage.getItem('token');

    try {
      const res = await fetch('/api/skills', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ label: tagName })
      });

      if (!res.ok && res.status !== 409) {
        const error = await res.json();
        throw new Error(error.error || 'Failed to add skill');
      }

      const newTag = { label: tagName };

      if (type === 'skill') {
        setSelectedSkills(prev => [...prev, newTag]);
        setUnmatchedSkills(prev => prev.filter(name => name !== tagName));
      } else if (type === 'preference') {
        setSelectedPreferences(prev => [...prev, newTag]);
        setUnmatchedPreferences(prev => prev.filter(name => name !== tagName));
      }
    } catch (err) {
      console.error(`Error adding new ${type}:`, err.message);
      alert('Failed to add tag: ' + err.message);
    }
  };

  const handleFinish = async () => {
    const token = localStorage.getItem('token');
    if (!token) return alert('You are not logged in.');

    try {
      const res = await fetch(`/api/organizations/${domain}/users/${user.username}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          ...formData,
          skills: selectedSkills,
          preferences: selectedPreferences,
        }),
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.error || 'Failed to update user');
      }

      navigate(`/PlanIt/${domain}/${managerUsername}/home`);
    } catch (err) {
      console.error('Error finishing update:', err);
      alert(err.message);
    }
  };

  return (
    <div>
      <Navbar user={managerUser} domain={domain} />
      <div className="generate-skills-screen">
        <h2>Describe the team member's experience</h2>
        <DescriptionBox
          name="teamMemberDescription"
          placeholder="e.g., Highly motivated developer with experience in React, testing, remote work..."
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          rows={5}
          width="100%"
          height="150px"
          fontSize="16px"
        />

        <MainButton title="Generate Tags" onClick={handleGenerate} size="small" />

        <div className="skill-preference-wrapper">
          <div className="skill-box">
            <h3>Skills</h3>
            { unmatchedSkills.length > 0 && (
              <>
                <h4>Our Suggestions</h4>
                <h11>Click on a suggestion below to add it as a new tag</h11>
                <TagsCollection
                  tags={unmatchedSkills}
                  isLabel={true}
                  onClick={(label) => handleAddNewTag(label, 'skill')}
                />
              </>
            )}
            <h4>Manual Selection</h4>
            <TagsInputContainer
              selectedTags={selectedSkills}
              setSelectedTags={setSelectedSkills}
              width={"400px"}
              height={"300px"}
            />
          </div>

          <div className="preference-box">
            <h3>Preferences</h3>
            { unmatchedPreferences.length > 0 && (
              <>
                <h4>Our Suggestions</h4>
                <h11>Click on a suggestion below to add it as a new tag</h11>
                <TagsCollection
                  tags={unmatchedPreferences}
                  isLabel={true}
                  onClick={(label) => handleAddNewTag(label, 'preference')}
                />
              </>
            )}
            <h4>Manual Selection</h4>
            <TagsInputContainer
              selectedTags={selectedPreferences}
              setSelectedTags={setSelectedPreferences}
              width={"400px"}
              height={"300px"}
            />
          </div>
        </div>

        <MainButton title="Save Changes" onClick={handleFinish} size="small" />
      </div>
    </div>
  );
};

export default EditTeamMemberSkills;
