import React from "react";
import "./SecondaryButton.css";
import 'bootstrap-icons/font/bootstrap-icons.css';

const SecondaryButton = ({ title, icon, onClick, type = "button" }) => {
    return (
      <button className="secondary-button-container" onClick={onClick} type={type}>
        <div className="secondary-button"> {title} 
        <span className="secondary-button-icon">{icon}</span>
        </div>
      </button>
    );
  };
  
  export default SecondaryButton;
  