import React from 'react';
import {
  PieChart, Pie, Cell,
  BarChart, Bar, XAxis, YAxis, Tooltip,
  ResponsiveContainer
} from 'recharts';
import './TeamMemberProjectStats.css';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

const ManagerProjectStats = ({ project, user }) => {
  console.log("Project data", project);

  if (!project || !project.tasks || !project.team) {
    return <div>No project data available...</div>;
  }

  // Prepare data for charts
  const taskStatusData = [
    { name: 'To Do', value: project.tasks.filter(task => task.status === 'To Do' && task.assignedTo._id === user._id).length },
    { name: 'In Progress', value: project.tasks.filter(task => task.status === 'In Progress' && task.assignedTo._id === user._id).length },
    { name: 'Done', value: project.tasks.filter(task => task.status === 'Done' && task.assignedTo._id === user._id).length },
  ];

  const priorityDistributionData = [
    { name: 'High', value: project.tasks.filter(task => task.priority === 'High' && task.assignedTo._id === user._id).length },
    { name: 'Medium', value: project.tasks.filter(task => task.priority === 'Medium' && task.assignedTo._id === user._id).length },
    { name: 'Low', value: project.tasks.filter(task => task.priority === 'Low' && task.assignedTo._id === user._id).length },
  ];

  console.log("Priority distribution data", priorityDistributionData);

  return (
    <div className="project-stats-container">
      <div className="project-stats-container-top">
      
      {/* Project Progress Gauge */}
      <div className="project-stats-section">
        <h4 className="project-stats-title">Project Progress</h4>
        <ResponsiveContainer width={300} height={300}>
          <PieChart>
            <Pie
              data={[{ name: 'Progress', value: project.progress }, { name: 'Remaining', value: 100 - project.progress }]}
              cx="50%" cy="50%" innerRadius={60} outerRadius={80} fill="#8884d8" dataKey="value"
            >
              <Cell key="progress" fill="#00C49F" />
              <Cell key="remaining" fill="#FFBB28" />
            </Pie>
          </PieChart>
        </ResponsiveContainer>
        {/* Legend for Project Progress */}
        <div className="project-progress-legend">
          <div className="legend-item">
            <span className="legend-color" style={{ backgroundColor: "#00C49F" }}></span>
            <span className="legend-label">Progress</span>
          </div>
          <div className="legend-item">
            <span className="legend-color" style={{ backgroundColor: "#FFBB28" }}></span>
            <span className="legend-label">Remaining</span>
          </div>
        </div>
      </div>

      {/* Task Status Breakdown */}
      <div className="project-stats-section">
        <h4 className="project-stats-title">Task Status Breakdown</h4>
        <ResponsiveContainer height={300}>
          <PieChart>
            <Pie
              data={taskStatusData}
              cx="50%" cy="50%" outerRadius={80} fill="#8884d8" dataKey="value" label
            >
              {taskStatusData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
              ))}
            </Pie>
          </PieChart>
        </ResponsiveContainer>
        {/* Legend for Task Status Breakdown */}
        <div className="task-status-legend">
          {taskStatusData.map((entry, index) => (
            <div key={index} className="legend-item">
              <span
                className="legend-color"
                style={{ backgroundColor: COLORS[index % COLORS.length] }}
              ></span>
              <span className="legend-label">{entry.name}</span>
            </div>
          ))}
        </div>
      </div>
      </div>
      <div className="project-stats-container-bottom">
        {/* Priority Distribution */}
        <div className="project-stats-section">
          <h4 className="project-stats-title">Priority Distribution</h4>
          <ResponsiveContainer width={300} height={300}>
            <PieChart>
              <Pie
                data={priorityDistributionData}
                cx="50%" cy="50%" outerRadius={80} fill="#8884d8" dataKey="value" label
              >
                {priorityDistributionData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
          {/* Legend for Priority Distribution */}
          <div className="priority-distribution-legend">
            {priorityDistributionData.map((entry, index) => (
              <div key={index} className="legend-item">
                <span
                  className="legend-color"
                  style={{ backgroundColor: COLORS[index % COLORS.length] }}
                ></span>
                <span className="legend-label">{entry.name}</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManagerProjectStats;
