import React, { useState } from 'react';
import './ForgotPassword.css';
const ForgotPassword = () => {
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    setMessage('');
  
    try {
      const res = await fetch('/api/send-reset-email', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email }),
      });
  
      let data = {};
      if (res.headers.get('content-type')?.includes('application/json')) {
        data = await res.json();
      }
  
      if (!res.ok) {
        setMessage(data.message || 'Something went wrong. Please try again later.');
        return;
      }
  
      setMessage(data.message || 'If an account with that email exists, a reset link has been sent.');
    } catch (err) {
      console.error('Error during request:', err);
      setMessage('Something went wrong. Please try again later.');
    } finally {
      setIsSubmitting(false);
    }
  };
  
  
  return (
    <div className="forgot-container">
      <div className="forgot-box">
        <h2 className="forgot-title">Find Your Account</h2>
        <p className="forgot-subtitle">Please enter your email address to search for your account.</p>

        <form onSubmit={handleSubmit}>
          <input
            className="forgot-input"
            type="email"
            placeholder="Email address"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <div className="forgot-actions">
            <button type="submit" className="search-btn" disabled={isSubmitting}>
              {isSubmitting ? 'Searching...' : 'Search'}
            </button>
            <button type="button" className="cancel-btn" onClick={() => window.history.back()}>
              Cancel
            </button>
          </div>
          {message && <p className="forgot-message">{message}</p>}
        </form>
      </div>
    </div>
  );
};

export default ForgotPassword;
