import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { socket } from '../../../../socket';
import InputBox from '../../../../components/InputBox/InputBox';
import MainButton from '../../../../components/Buttons/MainButton/MainButton';
import './SignIn.css';

const SignIn = () => {
  const navigate = useNavigate();

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [domain, setDomain] = useState(null);
  const [errorMessage, setErrorMessage] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  const togglePasswordVisibility = () => {
    setShowPassword((prev) => !prev);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!username || !password) {
      setErrorMessage('Please fill in both username and password.');
      return;
    }

    try {
      const res = await fetch('/api/tokens', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username, password }),
      });

      if (!res.ok) {
        setErrorMessage('Invalid username or password.');
        return;
      }

      const tokenData = await res.json();
      localStorage.setItem('token', tokenData.token);
      console.log('Token assigned:', tokenData.token);
      
      //Connect socket after saving token
      socket.auth.token = tokenData.token; // assign token to socket
      socket.connect(); // connect to backend

      // Fetch domain using token
      const domainRes = await fetch(`/api/organizations/users/${username}`, {
        method: 'GET',
        headers: { Authorization: `Bearer ${tokenData.token}` },
      });

      if (!domainRes.ok) {
        setErrorMessage('Failed to fetch domain.');
        return;
      }

      const organizationData = await domainRes.json();
      setDomain(organizationData.domain);
      console.log('Fetched domain:', organizationData.domain);

      navigate(`/PlanIt/${organizationData.domain}/${username}/home`);
    } catch (error) {
      console.error('Error during sign in:', error);
      setErrorMessage('An error occurred during sign in.');
    }
  };

  const handleForgotPassword = () => {
    navigate('/forgot-password');
  };

  return (
    <form className="auth-form" onSubmit={handleSubmit}>
      <h2 className="auth-title">Welcome Back!</h2>

      <div style={{ width: '70%' }}>
        <InputBox
          icon="bi-person-fill"
          placeholder="Username"
          type="username"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
          width="100%"
        />
      </div>

      <div style={{ position: 'relative', width: '70%' }}>
        <InputBox
          icon="bi-lock-fill"
          placeholder="Password"
          type={showPassword ? 'text' : 'password'}
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          width="100%"
        />
        <button
          type="button"
          className="toggle-password-visibility"
          onClick={togglePasswordVisibility}
          style={{
            position: 'absolute',
            right: '1px',
            top: '50%',
            transform: 'translateY(-50%)',
            background: 'none',
            border: 'none',
            cursor: 'pointer',
          }}
        >
          <i className={`bi ${showPassword ? 'bi-eye-slash-fill' : 'bi-eye-fill'}`}></i>
        </button>
      </div>

      <div className="error-message-container">
        {errorMessage && <p className="error-message">{errorMessage}</p>}
      </div>

      <div className="forgot-password-wrapper">
        <button type="button" className="forgot-password-link" onClick={handleForgotPassword}>
          Forgot Password?
        </button>
      </div>

      <MainButton title="Sign In" />
    </form>
  );
};

export default SignIn;
