import React, { useState, useEffect } from 'react';
import InputBox from '../../../../components/InputBox/InputBox';
import MainButton from '../../../../components/Buttons/MainButton/MainButton';
import Navbar from '../../../../components/Navbar/Navbar';
import { useNavigate, useParams } from 'react-router-dom';
import './RegistrationOfTeamMember.css';

const RegistrationOfTeamMember = ({ user }) => {
  const { domain, username } = useParams();
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    username: '',
    firstName: '',
    lastName: '',
    experience: '',
    profilePic: '',
  });
  const [errors, setErrors] = useState({});

  // Handle input field changes and clear error if fixed
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));

    setErrors((prev) => {
      const updated = { ...prev };

      // Clear specific error based on field and input
      if (name === 'email' && /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
        delete updated.email;
      }

      if ((name === 'firstName' || name === 'lastName') && /^[a-zA-Z]*$/.test(value)) {
        delete updated[name];
      }

      if (name === 'experience' && !isNaN(value) && value >= 0) {
        delete updated.experience;
      }

      // Always remove availability errors on change
      if (name === 'email' || name === 'username') {
        delete updated[name];
      }

      return updated;
    });
  };

  // Handle image upload
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        setFormData((prev) => ({ ...prev, profilePic: reader.result }));
      };
      reader.readAsDataURL(file);
    }
  };

  // Validate fields before submission
  const validateForm = () => {
    const newErrors = {};

    if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
      newErrors.email = "Invalid email format.";
    }
    if (!formData.firstName || !/^[a-zA-Z]+$/.test(formData.firstName)) {
      newErrors.firstName = "First name must contain only letters.";
    }
    if (!formData.lastName || !/^[a-zA-Z]+$/.test(formData.lastName)) {
      newErrors.lastName = "Last name must contain only letters.";
    }
    if (isNaN(formData.experience) || formData.experience < 0) {
      newErrors.experience = "Experience must be a non-negative number.";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // Check availability and navigate to next screen
  const handleNext = async (e) => {
    e.preventDefault();

    const isValid = validateForm();
    if (!isValid) return;

    const token = localStorage.getItem('token');
    if (!token) return alert('You are not logged in.');

    // Clear only availability-related errors
    setErrors((prev) => ({
      ...prev,
      email: '',
      username: ''
    }));

    try {
      console.log('Checking availability for:', formData.email, formData.username);
      const res = await fetch(`/api/users/check-availability`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          email: formData.email,
          username: formData.username,
        }),
      });

      const data = await res.json();

      const newErrors = {};
      if (data.emailTaken) {
        newErrors.email = 'Email is already in use.';
      }
      if (data.usernameTaken) {
        newErrors.username = 'Username is already taken.';
      }

      if (Object.keys(newErrors).length > 0) {
        setErrors((prev) => ({ ...prev, ...newErrors }));
        return;
      }

      navigate(`/PlanIt/${user.organization.domain}/${user.username}/generate-skills`, {
        state: { formData, user, domain }
      });

    } catch (err) {
      console.error('Error checking availability:', err);
      setErrors((prev) => ({
        ...prev,
        email: 'Could not verify email availability.',
        username: 'Could not verify username availability.',
      }));
    }
  };

  return (
    <div className="main-screen">
      <Navbar user={user} domain={domain} />
      <div className="main-container-registration">
        <form className="main-container-registration-form">
          <div className="main-left-registration">
            <h2 className="registration-title">Register Team Member</h2>
            <div className="registration-details">
              <h3>Personal Information</h3>
              <div className="personal-info">
                <InputBox
                  name="email"
                  icon="bi-envelope-fill"
                  placeholder="Email"
                  type="email"
                  value={formData.email}
                  onChange={handleChange}
                  width="60%"
                  borderColor={errors.email ? "#dc3545" : null}
                />
                <p className="field-error-text">{errors.email || ''}</p>

                <InputBox
                  name="username"
                  icon="bi-person-fill"
                  placeholder="Username"
                  type="text"
                  value={formData.username}
                  onChange={handleChange}
                  width="60%"
                  borderColor={errors.username ? "#dc3545" : null}
                />
                <p className="field-error-text">{errors.username || ''}</p>

                <InputBox
                  name="firstName"
                  icon="bi-person-fill"
                  placeholder="First Name"
                  type="text"
                  value={formData.firstName}
                  onChange={handleChange}
                  width="60%"
                  borderColor={errors.firstName ? "#dc3545" : null}
                />
                <p className="field-error-text">{errors.firstName || ''}</p>

                <InputBox
                  name="lastName"
                  icon="bi-person-fill"
                  placeholder="Last Name"
                  type="text"
                  value={formData.lastName}
                  onChange={handleChange}
                  width="60%"
                  borderColor={errors.lastName ? "#dc3545" : null}
                />
                <p className="field-error-text">{errors.lastName || ''}</p>

                <label htmlFor="experience">Years of experience:</label>
                <InputBox
                  name="experience"
                  icon="bi-clock-fill"
                  placeholder="Experience"
                  type="number"
                  value={formData.experience}
                  onChange={handleChange}
                  width="60%"
                  borderColor={errors.experience ? "#dc3545" : null}
                  min="0"
                />
                <p className="field-error-text">{errors.experience || ''}</p>
              </div>
            </div>
          </div>

          <div className="main-right-registration">
            <h3>Profile Picture</h3>
            <div className="profile-picture-container-member">
              <div
                className="profile-picture-circle-member"
                style={{ backgroundImage: `url(${formData.profilePic || ''})` }}
              ></div>
              <input type="file" name="profilePicture" onChange={handleFileChange} accept="image/*" lang="en" />
            </div>
          </div>
        </form>
        <div className="registration-button-container">
          <MainButton title="Next" onClick={handleNext} />
        </div>
      </div>
    </div>
  );
};

export default RegistrationOfTeamMember;
