import React from 'react';
import SectionBox from '../../../components/Section/SectionBox';
import ProjectsCollection from '../../../components/Projects/ProjectsCollection/ProjectsCollection';

const MyProjects = ({ projects, loggedUser, style }) => {
  const defaultStyle = { height: "350px" , width: "100%" };
  return (
    <SectionBox
      title={"My Projects (" + (projects ? projects.length : 0) + ")"}
      content={<ProjectsCollection projects={projects} loggedUser={loggedUser} />}
      style={style || defaultStyle}
    />
  );
};

export default MyProjects;
