import React from "react";
import "./InputBox.css";
import 'bootstrap-icons/font/bootstrap-icons.css';

const InputBox = ({
  name = "",
  icon, 
  placeholder = "",
  onChange = () => {},
  value = "",
  width = "500px",
  height = "40px",
  fontSize = "16px",
  maxLength,
  type = "text",
  readOnly = false,
  iconColor = "var(--text_secondary)",
  inputStyle = {},
  onClick = () => {},
  backgroundColor = "var(--background_primary)",
  borderColor = null,
}) => {

  return (
    <div className="input-box" style={{ width, height,   border: borderColor ? `2px solid ${borderColor}` : undefined, }}>
      <div className="input-icon">
      <i className={`bi ${icon}`} style={{ color: iconColor, fontSize: `calc(${fontSize} + 2px)`}} />
      <input
        name={name}
        type={type}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        maxLength={maxLength}
        readOnly={readOnly}
        style={{ fontSize, ...inputStyle }}
        onClick={onClick}
        backgroundColor={backgroundColor}
      />
      </div>
    </div>
  );
};

export default InputBox;
