import React from "react";
import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import './TaskScreen.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import Navbar from "../../components/Navbar/Navbar";
import TagsCollection from "../../components/Tags/TagsCollection/TagsCollection";
import TaskCommentsCollection from "../../components/TaskComments/TaskCommentsCollection/TaskCommentsCollection";
import SectionBox from "../../components/Section/SectionBox";
import InputBox from "../../components/InputBox/InputBox";

const TaskScreen = () => {
    const { domain, username, projectId, taskId } = useParams();

    const [user, setUser] = useState(null);
    const [task, setTask] = useState(null);
    const [comments, setComments] = useState([]);
    const [newComment, setNewComment] = useState("");
    const [status, setStatus] = useState("");

    useEffect(() => {
        const fetchUser = async () => {
            try {
                const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${localStorage.getItem('token')}`,
                    },
                });
                const data = await res.json();
                setUser(data);
            } catch (error) {
                console.error('Error fetching user:', error);
            }
        };

        fetchUser();
    }, [username]);

    useEffect(() => {
        const fetchTask = async () => {
            try {
                const res = await fetch(`/api/organizations/${domain}/users/${username}/tasks/${taskId}`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: `Bearer ${localStorage.getItem('token')}`,
                    },
                });
                const data = await res.json();
                setTask(data);
                setComments(data.comments);
                setStatus(data.status);
            } catch (error) {
                console.error('Error fetching task:', error);
            }
        };

        fetchTask();
    }, [taskId]);

    const handleCommentChange = (e) => {
        setNewComment(e.target.value);
    };

    const handleDeleteComment = (commentId) => {
        setComments((prev) => prev.filter((c) => c._id !== commentId));
    };

    const handleSendComment = async () => {
        if (!newComment.trim()) return;
        try {
            const res = await fetch(`/api/organizations/${domain}/users/${username}/tasks/${taskId}/comments`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
                body: JSON.stringify({ content: newComment }),
            });
            if (res.ok) {
                const updatedComment = await res.json();
                setComments((prevComments) => [...prevComments, updatedComment]);
                setNewComment("");
            } else {
                console.error('Failed to send comment:', await res.text());
            }
        } catch (error) {
            console.error('Error sending comment:', error);
        }
    };

    const handleStatusChange = async (e) => {
        const newStatus = e.target.value;
        setStatus(newStatus);
        try {
            const res = await fetch(`/api/organizations/${domain}/users/${username}/tasks/${taskId}/status`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
                body: JSON.stringify({ status: newStatus }),
            });
            if (!res.ok) {
                console.error('Failed to update status:', await res.text());
            }
        } catch (error) {
            console.error('Error updating status:', error);
        }
    };

    if (!user) return <div>No User Found...</div>;
    if (!task) return <div>No Task Found...</div>;

    return (
        <div>
            <Navbar user={user} domain={domain} />
            <div className="main-screen">
                <div className="main-task-screen">
                    <div className="main-left-task-screen">
                        <div className="task-screen-header">
                            <h2 className="task-screen-title">{task.title}</h2>
                            <h4 className="task-screen-priority">{task.priority + " Priority"}</h4>
                            <div className="task-screen-status">
                                <label className="task-screen-status-label" htmlFor="status-select">Status:</label>
                                <select
                                    id="status-select"
                                    className="task-screen-status-select"
                                    value={status}
                                    onChange={handleStatusChange}
                                >
                                    <option value="To Do">To Do</option>
                                    <option value="In Progress">In Progress</option>
                                    <option value="Done">Done</option>
                                </select>
                            </div>
                        </div>
                        <div className="task-screen-task-description">
                            <h4 className="task-screen-description">{task.description}</h4>
                        </div>
                        <div className="task-screen-tags">
                            <TagsCollection tags={task.tags} />
                        </div>
                    </div>
                    <div className="main-right-task-screen">
                        <SectionBox
                            title="Comments"
                            content={<TaskCommentsCollection comments={comments} domain={domain} username={username} onDelete={handleDeleteComment} />}
                            style={{ width: "100%" }}
                        />
                        <div className="task-screen-comments-input">
                            <InputBox
                                type="text"
                                className="task-screen-comments-input-field"
                                placeholder="Add a comment..."
                                value={newComment}
                                onChange={handleCommentChange}
                            />
                            <button
                                className="task-screen-comments-input-button"
                                onClick={handleSendComment}
                            >
                                Send
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default TaskScreen;
