import React, { useEffect, useState } from "react";
import './TaskComment.css';
import ProfilePic from "../ProfilePics/ProfilePic/ProfilePic";

const TaskComment = ({ comment, domain, username, projectId, taskId, onDelete }) => {
  const [author, setAuthor] = useState(null);

  useEffect(() => {
    const fetchAuthor = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${comment.author}`, {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        });
        const data = await res.json();
        setAuthor(data);
      } catch (error) {
        console.error('Error fetching author:', error);
      }
    };
    fetchAuthor();
  }, [comment.author]);

  const handleDelete = async () => {
    const confirmDelete = window.confirm("Are you sure you want to delete this comment?");
    if (!confirmDelete) return;

    try {
      const res = await fetch(`/api/organizations/${domain}/users/${username}/tasks/${taskId}/comments/${comment._id}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      });
      if (res.ok && onDelete) onDelete(comment._id);
    } catch (err) {
      console.error("Failed to delete comment:", err);
    }
  };

  if (!comment || !author) return null;

  return (
    <div className="task-comment-container">
      <div className="task-comment-header">
        <div className="task-comment-author">
          <ProfilePic size={30} picture={author.ProfilePic} />
          <h5>{author.firstName + ' ' + author.lastName}</h5>
        </div>
        <h5>{new Date(comment.date).toLocaleString('en-GB')}</h5>
        <button className="delete-comment-button" onClick={handleDelete}>
          <i className="bi bi-trash"></i>
        </button>
      </div>
      <div className="task-comment-content">
        <p>{comment.content}</p>
      </div>
    </div>
  );
};

export default TaskComment;
