import Navbar from "../../../components/Navbar/Navbar";
import React, { useState } from "react";
import 'bootstrap-icons/font/bootstrap-icons.css';
import MyTeam from "../Sections/MyTeam";
import MyNotifications from "../Sections/MyNotifications";
import MyProjects from "../Sections/MyProjects";
import "./ManagerMainScreen.css";
import SecondaryButton from "../../../components/Buttons/SecondaryButton/SecondaryButton";
import { useNavigate, useParams } from "react-router-dom";

const ManagerMainScreen = ({ user }) => {
  const { domain, username } = useParams();
  const navigate = useNavigate();
  const [notifications, setNotifications] = useState(user.notifications);

  const handleCreateProject = () => {
    navigate(`/PlanIt/${domain}/${username}/projects/create`);
  };

  const handleDeleteUser = async (member) => {
    const token = localStorage.getItem('token');
    try {
      const res = await fetch(`/api/organizations/${domain}/users/${member.username}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`
        }
      });

      if (!res.ok) throw new Error('Failed to delete user');
      alert(`${member.firstName} ${member.lastName} has been deleted.`);
      window.location.reload(); 
    } catch (error) {
      console.error("Error deleting user:", error);
      alert("Failed to delete user.");
    }
  };

  const handleNotificationRemove = async (notification) => {
    console.log("Remove notification:", notification._id);

    try {
      const res = await fetch(`/api/organizations/${domain}/users/${username}/notifications/${notification._id}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      });

      if (!res.ok) {
        throw new Error('Failed to delete notification');
      }

      // Remove notification from state
      setNotifications((prevNotifications) =>
        prevNotifications.filter((n) => n._id !== notification._id)
      );
    } catch (error) {
      console.error("Error deleting notification:", error);
      alert("Failed to delete notification.");
    }
  };

  return (
    <div className="manager-main-screen">
      <Navbar user={user} domain={domain} />
      <div className="main-container">
        <div className="main-left">
          <SecondaryButton title="Create Project" onClick={handleCreateProject} icon={<i className="bi bi-plus-lg"></i>} />
          <MyProjects projects={user.projects} loggedUser={user} style={{ width: "80%", height: "500px" }} />
        </div>
        <div className="main-right">
          <MyNotifications notifications={notifications} domain={domain} username={username} onRemove={handleNotificationRemove} />
          <MyTeam team={user.team} user={user} onDeleteUser={handleDeleteUser} />
        </div>
      </div>
    </div>
  );
};

export default ManagerMainScreen;
