import React, { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import InputBox from '../../../../components/InputBox/InputBox';
import MainButton from '../../../../components/Buttons/MainButton/MainButton';
import Navbar from '../../../../components/Navbar/Navbar';
import './RegistrationForManager.css';
import '../RegistrationOfTeamMember/RegistrationOfTeamMember.css';

const RegistrationForManager = ({ user }) => {
  const { username, domain } = useParams();
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    email: '',
    username: '',
    firstName: '',
    lastName: '',
    password: '',
    profilePic: '',
  });

  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
    setErrors((prev) => ({ ...prev, [name]: '' }));
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        setFormData((prev) => ({ ...prev, profilePic: reader.result }));
      };
      reader.readAsDataURL(file);
    }
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
      newErrors.email = 'Invalid email format.';
    }
    if (!formData.username || !/^[a-zA-Z]+$/.test(formData.username)) {
      newErrors.username = 'Username must contain only letters.';
    }
    if (!formData.firstName || !/^[a-zA-Z]+$/.test(formData.firstName)) {
      newErrors.firstName = 'First name must contain only letters.';
    }
    if (!formData.lastName || !/^[a-zA-Z]+$/.test(formData.lastName)) {
      newErrors.lastName = 'Last name must contain only letters.';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const sendResetEmail = async (userId, email) => {
    try {
      const res = await fetch('/api/send-reset-email', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ userId, email }),
      });
      const data = await res.json();
      console.log('Reset email response:', data);
    } catch (error) {
      console.error('Failed to send reset email:', error.message);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!validateForm()) return;

    const token = localStorage.getItem('token');
    if (!token) {
      alert('You are not logged in.');
      return;
    }

    try {
      const availabilityRes = await fetch(`/api/users/check-availability`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          email: formData.email,
          username: formData.username,
        }),
      });

      const data = await availabilityRes.json();
      const newErrors = {};
      if (data.emailTaken) newErrors.email = 'Email is already in use.';
      if (data.usernameTaken) newErrors.username = 'Username is already taken.';
      if (Object.keys(newErrors).length > 0) {
        setErrors((prev) => ({ ...prev, ...newErrors }));
        return;
      }

      const res = await fetch(`/api/organizations/${user.organization.domain}/users`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(formData),
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.error || 'Failed to register manager');
      }

      const newUser = await res.json();
      console.log('New manager created:', newUser);

      await sendResetEmail(newUser._id, newUser.email);

      navigate(`/PlanIt/${user.organization.domain}/${user.username}/home`);
    } catch (error) {
      console.error('Error registering manager:', error.message);
      alert(error.message);
    }
  };

  return (
    <div className="main-screen">
      <Navbar user={user} domain={domain} />
      <div className="main-container-registration">
        <form className="main-container-registration-form" onSubmit={handleSubmit}>
          <div className="main-left-registration-manager">
            <h2 className="registration-title-manager">Register Manager</h2>
            <div className="registration-details-manager">
              <h3>Personal Information</h3>
              <div className="personal-info-manager">
                {['email', 'username', 'firstName', 'lastName'].map((field) => (
                  <React.Fragment key={field}>
                    <InputBox
                      name={field}
                      icon={
                        field === 'email' ? 'bi-envelope-fill' : 'bi-person-fill'
                      }
                      placeholder={field.charAt(0).toUpperCase() + field.slice(1).replace(/([A-Z])/g, ' $1')}
                      type={field === 'email' ? 'email' : 'text'}
                      value={formData[field]}
                      onChange={handleChange}
                      width="60%"
                      borderColor={errors[field] ? '#dc3545' : null}
                    />
                    <p className="field-error-text">{errors[field] || ''}</p>
                  </React.Fragment>
                ))}
              </div>
            </div>
          </div>

          <div className="main-right-registration-manager">
            <h3>Profile Picture</h3>
            <div className="profile-picture-container-manager">
              <div
                className="profile-picture-circle-manager"
                style={{ backgroundImage: `url(${formData.profilePic || ''})` }}
              ></div>
              <input
                type="file"
                name="profilePic"
                onChange={handleFileChange}
                accept="image/*"
              />
            </div>
          </div>
        </form>
        <div className="button-container-manager">
            <MainButton title="Create Account" type="submit" />
          </div>
      </div>
    </div>
  );
};

export default RegistrationForManager;
