import React from "react";
import "./Tag.css";

const Tag = ({ tag, onClick, onRemove, isLabel }) => {
  if (!tag) {
    return <div></div>;
  }

  return (
    <div className="tag-container">
    <span
        className="tag-label"
        onClick={() => {
          if (onClick) {
            onClick(tag);
          }
        }}
      >  {isLabel ? tag : tag.label}     
      </span>
      {onRemove && (
        <span className="remove-tag" onClick={(e) => {
          e.stopPropagation(); 
          onRemove(tag);
        }}>
          ×
        </span>
      )}
    </div>
  );
};

export default Tag;
