import React from 'react';
import './ProfilePic.css';
import defaultPic from '../../../assets/default-profile-pic.png';

const ProfilePic = ({ picture, size = 60, iconColor = "var(--primary)"}) => {
  const style = {
    width: `${size}px`,
    height: `${size}px`,
    fontSize: `${size * 0.9}px`,
    border: '2px solid',
    borderColor: iconColor || 'var(--secondary)',
  };

  return picture ? (
    <img
      src={picture || defaultPic}
      className="profile-picture profile-picture-bordered"
      style={style}
      alt="profile"
    />
  ) : (
    <div
      className="profile-picture profile-picture-bordered profile-icon-wrapper"
      style={style}
    >
      <i className="bi bi-person" style={{ color: iconColor || 'var(--secondary)' }}></i>
    </div>
  );
};

export default ProfilePic;
