// src/components/Project.js

import React, { useState, useEffect } from 'react';
import './Project.css';
import ProfilePic from '../../ProfilePics/ProfilePic/ProfilePic';
import { useNavigate } from 'react-router-dom';

const ProfilesBox = ({ profiles }) => {
  return (
    <div className="profiles-box">
      {profiles.map((profile) => (
        <div className="profile-item" key={profile.id}>
          <ProfilePic src={profile.profilePic} size={60} />
        </div>
      ))}
    </div>
  );
};

const ProgressBar = ({ value = 0 }) => {
  return (
    <div className="progress-bar">
      <div className="progress-fill" style={{ width: `${value}%` }}>
        <span className="progress-text">{value}%</span>
      </div>
    </div>
  );
};

const Project = ({ project, loggedUser }) => {
  console.log('Project:', project);
  console.log('Logged User:', loggedUser);

  const navigate = useNavigate();

  const handleProjectClick = () => {
    // Navigate to the project details page
    console.log('Project clicked:', project);
    navigate(`/PlanIt/${loggedUser.organization.domain}/${loggedUser.username}/projects/${project._id}`);
  };
  
  if (!project) {
    return <div>No Project Found...</div>;
  }

  return (
    <div className="projet-container" onClick={handleProjectClick}>
      <h2 className="project-name">{project.title}</h2>
      <div className="profiles">
        <ProfilesBox profiles={project.team.map((memberId) => ({ id: memberId, profilePic: '' }))} />
      </div>
      <ProgressBar value={project.progress} />
    </div>
  );
};

export default Project;
