import React, { useState, useEffect } from 'react';
import InputBox from '../../../components/InputBox/InputBox';
import MainButton from '../../../components/Buttons/MainButton/MainButton';
import Navbar from '../../../components/Navbar/Navbar';
import { useNavigate, useParams } from 'react-router-dom';
import './EditTeamMemberFirstScreen.css';

const EditTeamMember = () => {
  const { domain, username, managerUsername } = useParams();
  const navigate = useNavigate();

  const [user, setUser] = useState(null);
  const [formData, setFormData] = useState(null);
  const [errors, setErrors] = useState({});
  const [managerUser, setManagerUser] = useState(null);

  useEffect(() => {
    const token = localStorage.getItem('token');

    const fetchUser = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
          method: 'GET',
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!res.ok) throw new Error('Failed to fetch user');
        const data = await res.json();
        setUser(data);
        setFormData({
          email: data.email,
          username: data.username,
          firstName: data.firstName,
          lastName: data.lastName,
          experience: data.experience,
          profilePic: data.profilePic,
        });
      } catch (error) {
        console.error('Error fetching user:', error.message);
      }
    };

    const fetchManager = async () => {
      try {
        const res = await fetch(`/api/organizations/${domain}/users/${managerUsername}`, {
          method: 'GET',
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!res.ok) throw new Error('Failed to fetch manager');
        const managerData = await res.json();
        setManagerUser(managerData);
      } catch (error) {
        console.error('Error fetching manager:', error.message);
      }
    };

    if (token) {
      fetchManager();
      fetchUser();
    }
  }, [username, domain, managerUsername]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        setFormData((prev) => ({ ...prev, profilePic: reader.result }));
      };
      reader.readAsDataURL(file);
    }
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) newErrors.email = "Invalid email format.";
    if (!formData.firstName || !/^[a-zA-Z]+$/.test(formData.firstName)) newErrors.firstName = "First name must contain only letters.";
    if (!formData.lastName || !/^[a-zA-Z]+$/.test(formData.lastName)) newErrors.lastName = "Last name must contain only letters.";
    if (isNaN(formData.experience) || formData.experience < 0) newErrors.experience = "Experience must be a non-negative number.";
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleNext = (e) => {
    e.preventDefault();
    if (validateForm()) {
      navigate(`/PlanIt/${domain}/${managerUsername}/${username}/edit-skills`, {
        state: { formData, user, domain, managerUser }
      });
    }
  };

  if (!formData || !user || !managerUser) return <p>Loading...</p>;

  return (
    <div className="main-screen">
      <Navbar user={managerUser} domain={domain} />
      <div className="main-container-registration">
        <form className="main-container-registration-form">
          <div className="main-left-registration">
            <h2 className="registration-title">Edit Team Member</h2>
            <div className="registration-details">
              <h3>Personal Information</h3>
              <div className="personal-info">
                <InputBox name="email" icon="bi-envelope-fill" placeholder="Email" type="email"
                  value={formData.email} onChange={handleChange} width="60%" readOnly
                  borderColor={errors.email ? "#dc3545" : null} />
                <p className="field-error-text">{errors.email || ''}</p>

                <InputBox name="username" icon="bi-person-fill" placeholder="Username" type="text"
                  value={formData.username} onChange={handleChange} width="60%" readOnly
                  borderColor={errors.username ? "#dc3545" : null} />
                <p className="field-error-text">{errors.username || ''}</p>

                <InputBox name="firstName" icon="bi-person-fill" placeholder="First Name" type="text"
                  value={formData.firstName} onChange={handleChange} width="60%"
                  borderColor={errors.firstName ? "#dc3545" : null} />
                <p className="field-error-text">{errors.firstName || ''}</p>

                <InputBox name="lastName" icon="bi-person-fill" placeholder="Last Name" type="text"
                  value={formData.lastName} onChange={handleChange} width="60%"
                  borderColor={errors.lastName ? "#dc3545" : null} />
                <p className="field-error-text">{errors.lastName || ''}</p>

                <label htmlFor="experience">Years of experience:</label>
                <InputBox name="experience" icon="bi-clock-fill" placeholder="Experience" type="number"
                  value={formData.experience} onChange={handleChange} width="60%"
                  borderColor={errors.experience ? "#dc3545" : null} min="0" />
                <p className="field-error-text">{errors.experience || ''}</p>
              </div>
            </div>
          </div>

          <div className="main-right-registration">
            <h3>Profile Picture</h3>
            <div className="profile-picture-container-member">
              <div className="profile-picture-circle-member"
                style={{ backgroundImage: `url(${formData.profilePic || ''})` }}></div>
              <input type="file" name="profilePicture" onChange={handleFileChange} accept="image/*" lang="en" />
            </div>
          </div>
        </form>
        <div className="registration-button-container">
          <MainButton title="Next" onClick={handleNext} />
        </div>
      </div>
    </div>
  );
};

export default EditTeamMember;
