import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import 'bootstrap-icons/font/bootstrap-icons.css';
import './ManagerProjectScreen.css';
import ProjectStatistics from './ProjectStats/ManagerProjectStats'
import Navbar from '../../../../components/Navbar/Navbar';
import MainButton from '../../../../components/Buttons/MainButton/MainButton';
import ProjectDescription from '../../../MainScreens/Sections/ProjectDescription';
import MyTasks from '../../../MainScreens/Sections/MyTasks';
import MyTeam from '../../../MainScreens/Sections/MyTeam';

const ManagerProjectScreen = ({ user, domain, project }) => {
  const navigate = useNavigate();

  const [tasks, setTasks] = useState(project.tasks || []);

  const handleDeleteTask = (deletedTaskId) => {
    setTasks(prev => prev.filter(task => task._id !== deletedTaskId));
  };

  const handleDeleteProject = async () => {
    const confirmDelete = window.confirm("Are you sure you want to delete this project?");
    if (!confirmDelete) return;
  
    const token = localStorage.getItem("token");
  
    try {
      const url = `/api/organizations/${domain}/users/${user.username}/projects/${project._id}`;
  
      const response = await fetch(url, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        }
      });
  
      if (response.ok) {
        alert("Project deleted successfully");
        navigate(`/PlanIt/${user.organization.domain}/${user.username}/home`);

      } else {
        alert("Failed to delete project (server error)");
      }
    } catch (err) {
      console.error("Failed to delete project:", err);
      alert("Failed to delete project (network error)");
    }
  };
  

  const handleEditProject = () => {
    navigate(`/PlanIt/${user.organization.domain}/${user.username}/projects/${project._id}/edit`);
  };

  return (
    <div className="manager-project-screen">
      <Navbar user={user} domain={domain} />
      <div className="manager-project-container">
        <div className="manager-project-left">
          <ProjectDescription project={project} />
          {/* Add project deadline display */}
          <div className="project-deadline">
            <strong>Deadline:</strong> {project.deadline ? new Date(project.deadline).toLocaleDateString() : 'No deadline specified'}
          </div>
          <div className="project-buttons-row">
            <MainButton
              title="Edit Project"
              onClick={handleEditProject}
              icon={<i className="bi bi-pencil-fill"></i>}
            />
            <MainButton
              title="Delete Project"
              onClick={handleDeleteProject}
              icon={<i className="bi bi-trash-fill"></i>}
            />
          </div>
          <MyTasks
            tasks={tasks}
            user={user}
            onDelete={handleDeleteTask}
            style={{ height: "300px", width: "90%" }}
            domain={domain}
            sectionTitle={"Tasks"}
          />

          <MyTeam team={project.team} user={user} style={{ width: "90%", height: "180px" }} sectionTitle={"Team"} />
        </div>
        <div className="manager-project-right">
          <ProjectStatistics project={project} />
        </div>
      </div>
    </div>
  );
};

export default ManagerProjectScreen;
