import React, { useEffect, useState } from 'react';
import './MyNotifications.css';
import SectionBox from '../../../components/Section/SectionBox';
import NotificationsCollection from '../../../components/Notifications/NotificationsCollection/NotificationsCollection'

const MyNotifications = ({ notifications, domain, username, onRemove }) => {
  const count = Array.isArray(notifications) ? notifications.length : 0;

  return (
    <SectionBox
      title={`My Notifications (${count})`}
      content={<NotificationsCollection notifications={notifications} domain={domain} username={username} onRemove={onRemove} />}
      style={{ width: "180%", height: "250px" }}
    />
  );
};

export default MyNotifications;
