import React, { useEffect, useState } from 'react';
import InputBox from '../../../components/InputBox/InputBox';
import MainButton from '../../../components/Buttons/MainButton/MainButton';
import Navbar from '../../../components/Navbar/Navbar';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import './EditManagerScreen.css';
const EditManager = () => {
  const { domain, username } = useParams();
  const navigate = useNavigate();
  const location = useLocation();

  const editorUser = location.state?.currentUser;

  const [formData, setFormData] = useState({
    email: '',
    username: '',
    firstName: '',
    lastName: '',
    profilePic: '',
  });

  const [formErrors, setFormErrors] = useState({});

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) return;

    fetch(`/api/organizations/${domain}/users/${username}`, {
      headers: { Authorization: `Bearer ${token}` }
    })
      .then(res => res.json())
      .then(data => setFormData({
        email: data.email || '',
        username: data.username || '',
        firstName: data.firstName || '',
        lastName: data.lastName || '',
        profilePic: data.profilePic || '',
      }))
      .catch(err => console.error('Error fetching manager:', err));
  }, [domain, username]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        setFormData(prev => ({ ...prev, profilePic: reader.result }));
      };
      reader.readAsDataURL(file);
    }
  };

  const validateForm = () => {
    const errors = {};
    if (!formData.email.trim()) errors.email = 'Email is required';
    if (!formData.username.trim()) errors.username = 'Username is required';
    if (!formData.firstName.trim()) errors.firstName = 'First name is required';
    if (!formData.lastName.trim()) errors.lastName = 'Last name is required';
    return errors;
  };

  const handleSubmit = async (e) => {
    console.log('Submitting form with data:', formData);
    e.preventDefault();
    const errors = validateForm();
    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }
    setFormErrors({});
    console.log('Form is valid, proceeding to update manager...');

    const token = localStorage.getItem('token');
    if (!token) return;

    try {
      console.log('Sending update request to server...');
      const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify(formData),
      });

      if (!res.ok) throw new Error('Failed to update manager');

      navigate(`/PlanIt/${domain}/${editorUser?.username}/home`);
    } catch (error) {
      console.error('Error updating manager:', error.message);
      alert(error.message);
    }
  };

  return (
    <div className="main-screen">
      <Navbar user={editorUser} domain={domain} />
      <div className="main-container-registration">
        <form className="main-container-registration-form">
          <div className="main-left-registration-manager">
            <h2 className="registration-title-manager">Edit Manager</h2>
            <div className="registration-details-manager">
              <h3>Personal Information</h3>
              <div className="personal-info-manager">
                <InputBox
                  icon="bi-envelope-fill"
                  placeholder="Email"
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  error={formErrors.email}
                  readOnly
                />
                <InputBox
                  icon="bi-person-fill"
                  placeholder="Username"
                  type="text"
                  name="username"
                  value={formData.username}
                  onChange={handleChange}
                  error={formErrors.username}
                  readOnly
                />
                <InputBox
                  icon="bi-person-fill"
                  placeholder="First Name"
                  type="text"
                  name="firstName"
                  value={formData.firstName}
                  onChange={handleChange}
                  error={formErrors.firstName}
                />
                <InputBox
                  icon="bi-person-fill"
                  placeholder="Last Name"
                  type="text"
                  name="lastName"
                  value={formData.lastName}
                  onChange={handleChange}
                  error={formErrors.lastName}
                />
              </div>
            </div>
          </div>
          <div className="main-right-registration-manager">
            <h3>Profile Picture</h3>
            <div className="profile-picture-container-manager">
              <div
                className="profile-picture-circle-manager"
                style={{
                  backgroundImage: `url(${formData.profilePic || ''})`,
                }}
              ></div>
              <input
                type="file"
                name="profilePic"
                onChange={handleFileChange}
                accept="image/*"
              />
            </div>
          </div>
        </form>
        <div className="button-container-manager">
          <MainButton title="Save Changes" onClick={handleSubmit} />
        </div>
      </div>
    </div>
  );
};

export default EditManager;
