import React from 'react';
import './SectionBox.css';

const SectionBox = ({ title, content, style, showViewAll = true, onViewAllClick }) => {
  return (
    <div className="container" style={style}>
      <div className="top-of-section">
        <h2 className="title">{title}</h2>
        {showViewAll && typeof onViewAllClick === 'function' && (
          <button className="view-all" onClick={onViewAllClick}>
            View All
          </button>
        )}
      </div>
      <div className="content">
        {content}
      </div>
    </div>
  );
};

export default SectionBox;
